import axios from 'axios';
import { Form, Formik } from 'formik';
import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { AVAUserDefault } from '../../assets';
import { AlertValidationForm, Cardwrapper } from '../../components';
import Button from '../../components/atoms/Button';
import { customMedia } from '../../components/Layouting/BreakPoints';
import { toastify, moneyFormatter, isAddress } from '../../utils';
import { dispatchTypes } from '../../utils/dispatchType';
import { showLoading } from '../../config/Redux/actions';

const UserReceiverPage = () => {
  const userState = useSelector((state) => state.userReducer.data);
  const { amount: saldo } = userState;
  const location = useLocation();
  const walletReceiver = location.pathname.split('/')[2];
  const token = localStorage.getItem('token');
  const idSender = localStorage.getItem('id');
  const [userReceiver, setUserReceiver] = useState({});
  const dispatch = useDispatch();
  const history = useHistory();


  useEffect(() => {
    dispatch(showLoading(true));
    const url = `${process.env.REACT_APP_BACKEND_API}/users${ isAddress(walletReceiver) ? '/wallet/' : '/@' }${walletReceiver}`;

    axios
      .get(url, {
        headers: { Authorization: `Bearer ${token}` } 
      })
      .then((res) => {
        if (res?.data?.data) {
          setUserReceiver(res.data.data);
        }
        dispatch(showLoading(false));
      })
      .catch((err) => {
        console.log(err.response);
        dispatch(showLoading(false)); 

      });
  }, [dispatch, token, walletReceiver]);

  const actionSubmitForm = (data) => {
    const valueAmountRequest = data.amount;
    if (parseFloat(valueAmountRequest) > parseFloat(saldo)) {
      return toastify(
        'Su balance no es suficiente para hacer esta transaccin',
        'error'
      );
    }
    let today = new Date();
    const dd = String(today.getDate()).padStart(2, '0');
    const mm = String(today.getMonth() + 1).padStart(2, '0'); //January is 0!
    const yyyy = today.getFullYear();
    today = mm + '/' + dd + '/' + yyyy;
    const dataSend = {
      sender: idSender,
      receiver: walletReceiver,
      amount: data.amount,
      balanceLeft: saldo - valueAmountRequest,
      isDraft: false,
      date: today,
      notes: data.notes,
    };
    dispatch({ type: dispatchTypes.setTransfer, payload: dataSend });
    // console.log(data);
    history.push(`/search-receiver/${walletReceiver}/confirmation`);
  };

  return (
    <Cardwrapper>
      <StyledUserReceiverPage>
        <div className="heading-section">
          <h2 className="text-heading">Enviar Dinero</h2>
        </div>
        <Cardwrapper>
          <div className="user-profile-receiver">
            <div className="avatar">
              <img
                src={
                  userReceiver?.avatar ? userReceiver?.avatar : AVAUserDefault
                }
                alt="username"
              />
            </div>
            <div className="description">
              <h2 className="text-heading">{userReceiver?.username || walletReceiver}</h2>
            </div>
          </div>
        </Cardwrapper>
        <div className="body-section">
          <Formik
            initialValues={{
              amount: null,
              notes: '',
            }}
            onSubmit={(values, { resetForm }) => {
              // console.log(values);
              actionSubmitForm(values);
              resetForm();
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isValid,
            }) => (
              <Form onSubmit={handleSubmit}>
                <p className="text-regular">
                  Ingrese el monto que quiere enviar y luego presione Continar 
                  para ir al proximo paso.
                </p>
                <div className="input-wrapper">
                  <input
                    className="amount"
                    type="text"
                    placeholder="0.00"
                    name="amount"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.amount}
                  />
                 
                  {errors.amount && touched.amount && errors.amount && (
                    <AlertValidationForm message={errors.amount} />
                  )}
                </div>
                <p
                  className={`text-heading summary ${
                    errors.amount && touched.amount && errors.amount
                      ? 'error'
                      : ''
                  }`}
                >
                  {moneyFormatter.format(saldo)}{' '}
                  {errors.amount && touched.amount && errors.amount && false
                    ? 'No Disponible'
                    : 'Disponible'}
                </p>
                <div className="btn-wrapper">
                  <Button
                    type="submit"
                    disabled={
                      !(values.amount) || (saldo >= parseFloat(values.amount) && parseFloat(values.amount) <= 0)
                    }
                    primary="primary"
                    className="btn-action"
                  >
                    Continuar
                  </Button>
                </div>
              </Form>
            )}
          </Formik>
        </div>
      </StyledUserReceiverPage>
      ;
    </Cardwrapper>
  );
};

export default UserReceiverPage;

const StyledUserReceiverPage = styled.div`
  padding: 30px;
  .heading-section {
    margin-bottom: 25px;
  }
  .user-profile-receiver {
    display: flex;
    gap: 1rem;
    padding: 15px;
    box-sizing: content-box;
    .avatar {
      width: 70px;
      height: 70px;
      img {
        object-fit: cover;
        width: 100%;
        height: 100%;
        border-radius: 10px;
      }
    }
    .description {
      display: flex;
      flex-direction: column;
      justify-content: center;
      p {
        margin-bottom: 0;
      }
    }
  }
  .body-section {
    margin-top: 50px;
    form {
      .text-regular {
        max-width: 400px;
        margin-bottom: 50px;
      }
      .input-wrapper {
        text-align: center;
        margin-bottom: 39px;
        .amount {
          font-family: Nunito Sans;
          font-style: normal;
          font-weight: bold;
          font-size: 42px;
          line-height: 56px;
          text-align: center;
          border: 0;
          background-color: transparent;
          width: 100%;
          &::placeholder {
            color: #b5bdcc;
          }
          &:focus {
            outline: none;
          }
          &:valid {
            color: #6379f4;
          }
        }
      }
      p.summary {
        text-align: center;
        margin-bottom: 60px;
        &.error {
          color: red;
        }
      }
      .notes-wrapper {
        /* background-color: yellow; */
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: center;
        .error {
          width: max-content;
        }
        ${customMedia.greaterThan('620px')`
              .input {
                /* background-color: pink; */
                width: 30%;
              }
          `}
        

      }
      .btn-wrapper {
        margin-top: 58px;
        display: flex;
        justify-content: flex-end;
        .btn-action {
          width: 170px;
          ${customMedia.lessThan('620px')`
            width: 100%; 
          `}
        }
      }
    }
  }
`;
