// import { Public } from '@material-ui/icons';
import { BrowserRouter as Router, Route, Switch } from 'react-router-dom';

import {
  HistoryPage,
  Homepage,
  Landingpage,
  LoginPage,
  TopupPage,
  WalletPage,
  ToolsPage,
  PublicProfilePage,
  P2PPage,
  CreateAdPage,
  OrdersPage,
  OrderPage,
  ProfileUserPage
} from '../../page';
import MemberPrivateRoute from './MemberPrivateRoute';
import PublicRoute from './PublicRoute';

const Routes = () => {
  return (
    <Router>
      <Switch>
        <Route exact path="/" component={Landingpage} />
        {/* START = AUTHENTICATION PAGE */}
        <Route path="/0x:id" component={PublicProfilePage} />

        <MemberPrivateRoute path="/ads" component={CreateAdPage} />
        <MemberPrivateRoute path="/orders" component={OrdersPage} />

        <PublicRoute exact path="/login" component={LoginPage} />
  
        <MemberPrivateRoute path="/dashboard" component={Homepage} />
        <MemberPrivateRoute path="/exchange" component={P2PPage} />
        <MemberPrivateRoute path="/history" component={HistoryPage} />
        <MemberPrivateRoute path="/topup" component={TopupPage} />
        <MemberPrivateRoute path="/wallet" component={WalletPage} />
        <MemberPrivateRoute path="/tools" component={ToolsPage} />
        <MemberPrivateRoute path="/home" component={Homepage} />
        <MemberPrivateRoute path="/order/:id" component={OrderPage} />
        <MemberPrivateRoute exact path="/account" component={CreateAdPage} />
   
    
       

      </Switch>
    </Router>
  );
};

export default Routes;
