import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import { OrderStatusEnum, OrderStatusIcon, call, compareAddresses, send } from '../../../utils';
import Button from '../Button';

const Order = ({ orderId, address }) => {
    const [order, setOrder] = useState([]);

    const callOrder = async (id) => {
        let orderData = await call('orders', [id]);
        if (!orderData) {
            return;
        }
        const ad = await call('ads', [orderData.adId]);
        console.log('ORDER', { ...orderData, ad });
        setOrder({ ...orderData, ad });
    };

    useEffect(() => {
        callOrder(orderId);
        // eslint-disable-next-line
    }, []);
    
    const handleOrderAction = async (action, id) => {
        try {
            const result = await send(action, [id]);
            console.log(result);
            await callOrder(orderId);
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <StyledOrder key={order.orderId}>
            <div>
                <span className="order-status">
                    {OrderStatusIcon[order.status]} <b>{OrderStatusEnum[order.status]}</b>
                </span>
                <span className="last-update">{new Date(Number(order.updatedAt) * 1000).toLocaleString()}</span>
            </div>
            <div>
                <span>{order.value}</span>
                <span>{order.currency}</span>
            </div>
            <div>
                <span>{new Date(Number(order.createdAt) * 1000).toLocaleString()}</span>
            </div>
            <div>
                {compareAddresses(address, order.receiver) && ['0', '2'].includes(order.status) ? (
                    <Button onClick={() => handleOrderAction('fiatTransfered', order.orderId)}>Dinero Transferido</Button>
                ) : null}
                {(compareAddresses(address, order.receiver) && ['0', '1', '2'].includes(order.status)) ||
                (compareAddresses(address, order.sender) && order.status === '0' && (new Date() - new Date(Number(order.createdAt) * 1000)) / (1000 * 60) > 30) ? (
                    <Button onClick={() => handleOrderAction('cancelOrder', order.orderId)}>Cancelar</Button>
                ) : null}
                {compareAddresses(address, order.sender) && ['1', '2'].includes(order.status) ? (
                    <Button onClick={() => handleOrderAction('releaseOrder', order.orderId)}>He recibido el dinero</Button>
                ) : null}

                {order.status === '1' && (new Date() - new Date(Number(order.createdAt) * 1000)) / 1000 > order.ad.expirationTime ? <Button>Abrir Disputa</Button> : null}
            </div>
        </StyledOrder>
    );
};

Order.propTypes = {
    orderId: PropTypes.string,
    address: PropTypes.string
};

export default Order;

const StyledOrder = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 10px 15px;
    
    div {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .order-status {
        display: flex;
        align-items: center;
        font-size: 15px;
    }

    .order-status b {
        font-size: 18px;
        margin-left: 8px;
    }

    .last-update {
        color: #bdbdbd;
        font-size: 14px;
    }

    .table-headers {
        width: 100%;
        justify-content: space-between;
    }
`;
