import React from 'react';
import styled from 'styled-components';
import { BGSideAuth, WalletApp } from '../../../assets';
import { LogoBrand } from '../../atoms';
import { customMedia } from '../../Layouting/BreakPoints';

const SidebarAuth = ({ className }) => {
  return (
    <Styles className={className}>
      {/* <div className="image-background"></div> */}
      <div className="wrapper">
        <LogoBrand light />
        <div className="image-wrapper">
          <img src={WalletApp} alt="walletapp" />
        </div>
      </div>
    </Styles>
  );
};

export default SidebarAuth;

const Styles = styled.div`
  background-color: #6379f4;
  height: 100vh;
  width: 100%;
  position: relative;
  background-image: url(${BGSideAuth});
  .wrapper {
    z-index: 9;
    padding: 0 150px;
    ${customMedia.lessThan('laptop')`
      padding-left: 50px; 
    `}
    ${customMedia.lessThan('tablet')`
      padding: 50px;
    `}
    padding-top: 50px;
    /* width: 750px; */
    .logo {
      padding-top: 10px;
      font-style: normal;
      font-weight: bold;
      font-size: 29px;
      line-height: 40px;
      color: #ffffff;
    }
    .image-wrapper {
      width: 600px;
      ${customMedia.lessThan('tablet')`
      width: 100%;
    `}
      img {
        width: auto;
        height: auto;
        ${customMedia.lessThan('laptop')`
     width: 90%;
    `}
        ${customMedia.lessThan('1000px')`
     width: 80%;
    `}
        ${customMedia.lessThan('900px')`
      width: 70%;
    `}
        ${customMedia.lessThan('tablet')`
      width: 70%;
    `}
      }
    }
    .paragraph {
      .title-paragraph {
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        color: #ffffff;
        top: 20px;
      }
      .text {
        font-style: normal;
        font-weight: normal;
        font-size: 16px;
        padding-top: 30px;
        color: rgba(255, 255, 255, 0.8);
      }
    }
  }
`;
