import React from 'react';
import { Route, useLocation } from 'react-router-dom';
import { Footer, MenuAsideLeft, Navbar } from '../../components';
import { StylingMainContent } from './StyledPrivateRoute';

const PrivateRoute = ({ component: Component, ...rest }) => {
  const location = useLocation();

  // START = LOGIC FOR ASIDE MENU ACTIVE
  const pathNow = location.pathname;
  const listMenu = {
    [`/dashboard`]: 'dashboard',
    [`/history`]: 'dashboard',
    [`/search-receiver`]: 'dashboard',
    [`/search-receiver/to-username`]: 'dashboard',
    [`/status-transfer`]: 'dashboard',
    [`/search-receiver/to-username/confirmation`]: 'dashboard',
    [`/topup`]: 'topup',
    [`/tools`]: 'tools',
    [`/exchange`]: 'exchange',
    [`/orders`]: 'orders',
    [`/wallet`]: 'wallet',
    [`/profile`]: 'profile',
    [`/account`]: 'account',
    [`/profile/info`]: 'profile',
    [`/profile/password`]: 'profile',
    [`/profile/change-pin`]: 'profile',
    [`/profile/add-phone-number`]: 'profile',
  };
  // console.log(listMenu);
  // END = LOGIC FOR ASIDE MENU ACTIVE
  // console.log(location);
  return (
    <Route
      {...rest}
      render={(props) => {
          return (
            <>
              <Navbar />
              <StylingMainContent>
                <MenuAsideLeft active={listMenu[pathNow]}/>
                {/* Styling for this element (Main, Header-setion) inside StylingMainContent component */}
                <div className="main">
                  <Component {...props} />
                </div>
              </StylingMainContent>
              <Footer />
            </>
          );
       
      }}
    />
  );
};

export default PrivateRoute;
