import React, { useEffect, useState } from "react";
import { useHistory } from "react-router-dom";
import detectEthereumProvider from '@metamask/detect-provider'
import styled from "styled-components";
import {
  Button,
  SidebarAuth,
} from "../../components";
import { customMedia } from "../../components/Layouting/BreakPoints";
const LoginPage = () => {
  const history = useHistory();

  const initialState = { accounts: [], balance: "", chainId: "" }  /* Updated */
  const [wallet, setWallet] = useState(initialState)

  useEffect(() => {
    if (wallet.accounts.length > 0) {
      history.push(`/dashboard`);
    }
    // eslint-disable-next-line
  }, [wallet]);

  useEffect(() => {
    const refreshAccounts = (accounts) => {
      if (accounts.length > 0) {
        updateWallet(accounts)
      } else {
        // if length 0, user is disconnected
        setWallet(initialState)
      }
    }

    const refreshChain = (chainId) => {               /* New */
      setWallet((wallet) => ({ ...wallet, chainId }))      /* New */
    }                                                      /* New */

    const getProvider = async () => {
      const provider = await detectEthereumProvider({ silent: true })

      if (provider) {                                           
        const accounts = await window.ethereum.request(
          { method: 'eth_accounts' }
        )
        refreshAccounts(accounts)
        window.ethereum.on('accountsChanged', refreshAccounts)
        window.ethereum.on("chainChanged", refreshChain)  /* New */
      }
    }

    getProvider()

    return () => {
      window.ethereum?.removeListener('accountsChanged', refreshAccounts)
      window.ethereum?.removeListener("chainChanged", refreshChain)  /* New */
    }
    // eslint-disable-next-line
  }, [])

  const updateWallet = async (accounts) => {
    const balance = await window.ethereum.request({   /* New */
      method: "eth_getBalance",                                      /* New */
      params: [accounts[0], "latest"]})                               /* New */ 
    const chainId = await window.ethereum.request({                 /* New */ 
      method: "eth_chainId",                                         /* New */ 
    })                                                               /* New */ 
    setWallet({ accounts, balance, chainId })                        /* Updated */ 
  }

  const handleConnect = async () => {
    let accounts = await window.ethereum.request({
      method: "eth_requestAccounts",
    })
    updateWallet(accounts)
  }

  return (
    <Styles>
      <div className="row-side">
        <SidebarAuth />
      </div>
      <div className="form-side">
        <div className="content">
          <h5>
            Comiense a Administrar su Dinero
            <br />
            Desde todas sus Plataformas Favoritas
            <br />
            Con 10,000+ Usuarios
            <br />
          </h5>
          <h6>
            Transfiere Dinero a tus Amigos <br />
            desde Wallet estes donde estes. Disponible en App y Web
            <br />
            Sin comisiones!
          </h6>
                <Button
                 
                onClick={handleConnect}
                  className="button-login"
                >
                   { window.ethereum?.isMetaMask && wallet.accounts.length < 1 ?
                          'Connect MetaMask' : 'Connect Wallet'
                        }
                  
                </Button>
        
        </div>
      </div>
    </Styles>
  );
};

export default LoginPage;

const Styles = styled.div`
  height: 100vh;
  display: flex;
  ${customMedia.lessThan("tablet")`
  flex-direction: column
`}

  .row-side {
    /* height: 100%; */
    width: 55%;
    ${customMedia.lessThan("tablet")`
 width: 100%
  `}
    ${customMedia.lessThan("mobile")`
    display: none;
   `}
  }
  .form-side {
    /* width: 50%; */
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    ${customMedia.lessThan("tablet")`
 width: 100%;
  `}
    ${customMedia.lessThan("mobile")`
    padding: 30px 0;
   `}
    .content {
      width: 80%;
      h5 {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 33px;
        color: #3a3d42;
      }
      h6 {
        font-style: normal;
        margin-bottom: 30px;
        margin-top: 20px;
        font-weight: normal;
        font-size: 16px;
        line-height: 30px;
        color: rgba(58, 61, 66, 0.6);
      }
      form {
        margin-top: 40px;
        .form-input {
          width: 100%;
          margin-bottom: 40px;
        }
        .forgot-pass {
          margin-top: 20px;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          text-align: right;
          &:hover {
            opacity: 0.5;
          }
        }
        .button-login {
          margin-top: 50px;
        }
      }
    }
  }
  .register-link {
    margin-top: 20px;
    font-weight: 600;
    display: flex;
    gap: 5px;
    justify-content: center;
    p {
      margin-bottom: 0;
    }
  }
`;
