import axios from 'axios';
import styled from 'styled-components';
import QRCode from 'qrcode.react';
import React, { useEffect, useState } from 'react';
import { Cardwrapper, HeadingContent } from '../../components';
import { useHistory, useParams } from 'react-router-dom';
import { AVAUserDefault } from '../../assets';
import { LogoBrand } from '../../components/atoms';
import { customMedia } from '../../components/Layouting/BreakPoints';
import { colors } from '../../utils';

const PublicProfilePage = () => {

    let { id } = useParams();

    const [userData, setUserData] = useState({});

    const { avatar, walletAddress, username } = userData;

    const history = useHistory();
    const actionLogin = () => {
        history.push('/login');
    };
    const actionRegister = () => {
      history.push("/sign-up");
    };

    useEffect(() => {
        axios.get(`${process.env.REACT_APP_BACKEND_API}/users/@${id}`)
        .then((res) => {
            console.log(res)
            setUserData(res.data.data)
        })
        .catch((err) => {
            console.log(err);
        });
    }, [id]);

  
  return (
    <>
      <StyledPublicNavBar>
      <div className="heading-section">
        <nav>
          <div className="custom-container">
            <LogoBrand />
            <div className="btn-wrapper">
              <button className="button default" onClick={actionLogin}>
                Entrar
              </button>
              <button className="button filled" onClick={actionRegister}>
                Registro
              </button>
            </div>
          </div>
        </nav>
      </div>
      </StyledPublicNavBar>
    <StyledPublicProfilePage>
    <div className='main-wrapper'>
        <Cardwrapper>
            <HeadingContent>
                
            <div className="image-wrapper">
              <img src={avatar ? avatar : AVAUserDefault} alt="user" />
            <h3 className="username">@{username}</h3>
            </div>
            </HeadingContent>
                <div className="body-content">
                <div className="body-content">
            <Cardwrapper className="item">
                <p className="text-regular">
                   <sm style={{fontSize: 12}}> Enva <b>TetherUSD (USDT)</b> de la red <b>Polygon</b> a esta direccin: </sm><br></br> <b>{walletAddress}</b>
                </p>
            </Cardwrapper>
            <div className="item d-flex flex-column align-items-center justify-content-center">
            <QRCode value={walletAddress} size={200} />
          </div>
            </div>
            </div>
        </Cardwrapper>
        </div>
    </StyledPublicProfilePage>
    </>
  );
};

export default PublicProfilePage;

const StyledPublicNavBar = styled.div`
/* START = NAVBAR */
nav {
  
  position: fixed;
  width: 100vw;
  top: 0;
  background-color: white;
  box-shadow: 0px 6px 40px rgb(24 23 23 / 10%);
  z-index: 9;
  .custom-container {
    padding: 10px 70px;
    display: flex;
    width: 90%;
    justify-content: space-between;
    align-items: center;
    ${customMedia.lessThan('mobile')`
    padding: 10px;
   padding-left: 0;
   padding-right: 0;
`}

    .logo {
      font-style: normal;
      font-weight: bold;
      font-size: 29px;
      line-height: 40px;
      color: ${colors.primary};
      ${customMedia.lessThan('mobile')`
  font-size: 25px;
   padding-right: 10px;
`}
      ${customMedia.lessThan('minimobile')`
  font-size: 20px;
`}
    }
    .btn-wrapper {
      display: flex;
      gap: 1rem;
      z-index: 9;
      .button {
        width: 120px;
        height: 35px;
        border: 15px;
        border-radius: 8px;
        display:flex;
        align-items: center;
        justify-content: center;
        font-weight: 700;
        ${customMedia.lessThan('mobile')`
          width: 80px;
          height: 30px;
          font-size: 13px;
          border-radius: 8px;
          letter-spacing: 0.5px;
        `}
        ${customMedia.lessThan('minimobile')`
          width: 50px;
          height: 30px;
        `}
        &.default {
          background-color: transparent;
          border: 1px solid ${colors.primary};
          color: ${colors.primary};
        }
        &.filled {
          background-color: ${colors.primary};
          color: white;
          border: 0;
        }
      }
    }
  }
}
/* END = NAVBAR */
`;

const StyledPublicProfilePage = styled.div`
    .main-wrapper {
        margin: 0 auto;
        margin-top: 50px;
    }
    .publicKey, .text-regular {
      font-size: 15px;
      word-break: break-all;
      margin-bottom: 1rem;
    }
    .main-wrapper > div {
        padding: 30px;
    }
    .body-content {
        justify-content: center;
    align-items: center;
    width: 100%;
    }
    .main-wrapper .text-heading {
        display: flex;
    justify-content: center;
    align-items: center;
    }
  padding: 50px;
  .image-wrapper {
    display: flex;
    align-items: center;
    justify-content: center;
  }
  .image-wrapper h3 {
    margin: 0;
  }
  .image-wrapper img {
    max-width: 70px;
    height: auto;
    margin-right: 10px;
  }
  .body-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .item {
      padding: 30px 20px;
      display: flex;
      gap: 1rem;
      p {
        margin-bottom: 0;
      }
      .number {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #6379f4;
      }
    }
  }
`;
