import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useHistory, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import axios from 'axios';
import {
  Button,
  CardProfileUser,
  Cardwrapper,
  HeadingContent,
  AlertValidationForm,
  Modal,
  Input
} from '../../components';
import { customMedia } from '../../components/Layouting/BreakPoints';
import { colors, moneyFormatter, toastify, isAddress, decipherData } from '../../utils';
import { dispatchTypes } from '../../utils/dispatchType';
import { showLoading } from '../../config/Redux/actions';
import { ERCToken } from '../../config/config';
import { Form, Formik } from "formik";
import * as Web3 from 'web3';

const ConfirmationTransferPage = () => {
  const userState = useSelector((state) => state.userReducer.data);
  console.log(userState)
  const { id} = userState;
  const transferReducer = useSelector((state) => state.transferReducer);
  const [isShowModal, setIsShowModal] = useState(false);
  const history = useHistory();
  const dispatch = useDispatch();
  const token = localStorage.getItem('token');
  const [userReceiver, setUserReceiver] = useState({});
  const location = useLocation();
  const walletReceiver = location.pathname.split('/')[2];
  const [isShowPassword, setIsShowPassword] = useState(false);
  
  const actionShowPassword = () => {
    isShowPassword ? setIsShowPassword(false) : setIsShowPassword(true);
  };

  useEffect(() => {
    dispatch(showLoading(true));
    const url = `${process.env.REACT_APP_BACKEND_API}/users${ isAddress(walletReceiver) ? '/wallet/' : '/@' }${walletReceiver}`;

    axios
      .get(url, {
        headers: { Authorization: `Bearer ${token}` } 
      })
      .then((res) => {
        let user = {
          walletAddress: walletReceiver
        }
        if (res?.data?.data?.walletAddress) {
          user = res.data.data;
        }
        setUserReceiver(user);
        dispatch(showLoading(false));
      })
      .catch((err) => {
        if (err.response.status === 401) {
          dispatch({ type: dispatchTypes.setUserLogout });
          localStorage.removeItem('isAuth');
          history.push('/');
        }
        console.log(err.response);
        dispatch(showLoading(false)); 

      });
      // eslint-disable-next-line
  }, []);
  // END = GET USER RECEIVER
  const actionButton = () => {
    setIsShowModal(true);
  };

  const randomUint256 = () => {
    let result = '';
    for (let i = 0; i < 10; i++) {
        result += Math.floor(Math.random() * 10);
    }
    return parseInt(result);
}

  // VALIDATE PIN
  const checkPinAction = async (data) => {
    dispatch(showLoading(true)); 
    const ownerData = await axios.get(`${process.env.REACT_APP_BACKEND_API}/users/key/${id}`, { headers: { Authorization: `Bearer ${token}` } });
    const { ownerKey, ownerAddress, walletAddress: ownerWalletAddress } = ownerData?.data?.data?.[0] || {};
    const privateKey = decipherData(data.password, ownerKey)
    if (!privateKey) {
      return toastify('Contrasea Incorrecta', 'error');
    }
    
    const web3 = new Web3(ERCToken.JSON_RPC_ENDPOINT);
    const amountToWei = transferReducer.amount * (10 ** ERCToken.TOKEN_DECIMALS);
    const transferABI = [{"inputs":[{"internalType":"address","name":"recipient","type":"address"},{"internalType":"uint256","name":"amount","type":"uint256"}],"name":"transfer","outputs":[{"internalType":"bool","name":"","type":"bool"}],"stateMutability":"nonpayable","type":"function"}];
    const transferContract = new web3.eth.Contract(transferABI, ERCToken.TOKEN_CONTRACT, {from: ownerWalletAddress});
    const encodedTransferABI = await transferContract.methods.transfer(userReceiver.walletAddress, amountToWei.toString()).encodeABI();

    const CONTRACT_ABI = [
      {
          "inputs": [
              {
                  "internalType": "address",
                  "name": "_owner",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "_ownerRecovery",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "_executor",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "_executorRecovery",
                  "type": "address"
              }
          ],
          "stateMutability": "nonpayable",
          "type": "constructor"
      },
      {
          "inputs": [],
          "name": "ReentrancyGuardReentrantCall",
          "type": "error"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              },
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "oldAddress",
                  "type": "address"
              },
              {
                  "indexed": false,
                  "internalType": "address",
                  "name": "newAddress",
                  "type": "address"
              }
          ],
          "name": "ApproveAddressChange",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "uint256",
                  "name": "txIndex",
                  "type": "uint256"
              }
          ],
          "name": "ConfirmTransaction",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "uint256",
                  "name": "txIndex",
                  "type": "uint256"
              },
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "to",
                  "type": "address"
              },
              {
                  "indexed": false,
                  "internalType": "uint256",
                  "name": "value",
                  "type": "uint256"
              },
              {
                  "indexed": false,
                  "internalType": "bytes",
                  "name": "data",
                  "type": "bytes"
              },
              {
                  "indexed": false,
                  "internalType": "bool",
                  "name": "ownerConfirm",
                  "type": "bool"
              },
              {
                  "indexed": false,
                  "internalType": "bool",
                  "name": "isCommerce",
                  "type": "bool"
              }
          ],
          "name": "CreateTransaction",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              },
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "newAddress",
                  "type": "address"
              }
          ],
          "name": "DeleteAddressChange",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "uint256",
                  "name": "txIndex",
                  "type": "uint256"
              }
          ],
          "name": "DeleteTransaction",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": false,
                  "internalType": "uint256",
                  "name": "amount",
                  "type": "uint256"
              },
              {
                  "indexed": false,
                  "internalType": "uint256",
                  "name": "balance",
                  "type": "uint256"
              }
          ],
          "name": "Deposit",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "uint256",
                  "name": "txIndex",
                  "type": "uint256"
              }
          ],
          "name": "ExecuteTransaction",
          "type": "event"
      },
      {
          "anonymous": false,
          "inputs": [
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "sender",
                  "type": "address"
              },
              {
                  "indexed": true,
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              },
              {
                  "indexed": true,
                  "internalType": "address",
                  "name": "oldAddress",
                  "type": "address"
              },
              {
                  "indexed": false,
                  "internalType": "address",
                  "name": "newAddress",
                  "type": "address"
              }
          ],
          "name": "ProposeAddressChange",
          "type": "event"
      },
      {
          "inputs": [
              {
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              }
          ],
          "name": "approveAddressChange",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              }
          ],
          "name": "confirmTransaction",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              }
          ],
          "name": "deleteAddressChange",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              }
          ],
          "name": "deleteTransaction",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              },
              {
                  "internalType": "address",
                  "name": "_to",
                  "type": "address"
              },
              {
                  "internalType": "uint256",
                  "name": "_value",
                  "type": "uint256"
              },
              {
                  "internalType": "bytes",
                  "name": "_data",
                  "type": "bytes"
              },
              {
                  "internalType": "bool",
                  "name": "_isCommerce",
                  "type": "bool"
              },
              {
                  "internalType": "address",
                  "name": "_tokenAddress",
                  "type": "address"
              }
          ],
          "name": "draftTransaction",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              }
          ],
          "name": "executeTransaction",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [],
          "name": "getIndexes",
          "outputs": [
              {
                  "internalType": "uint256[]",
                  "name": "",
                  "type": "uint256[]"
              }
          ],
          "stateMutability": "view",
          "type": "function"
      },
      {
          "inputs": [],
          "name": "getRoleAddresses",
          "outputs": [
              {
                  "internalType": "address",
                  "name": "owner",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "ownerRecovery",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "executor",
                  "type": "address"
              },
              {
                  "internalType": "address",
                  "name": "executorRecovery",
                  "type": "address"
              }
          ],
          "stateMutability": "view",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              }
          ],
          "name": "getTransaction",
          "outputs": [
              {
                  "components": [
                      {
                          "internalType": "address",
                          "name": "to",
                          "type": "address"
                      },
                      {
                          "internalType": "uint256",
                          "name": "value",
                          "type": "uint256"
                      },
                      {
                          "internalType": "bytes",
                          "name": "data",
                          "type": "bytes"
                      },
                      {
                          "internalType": "bool",
                          "name": "ownerConfirm",
                          "type": "bool"
                      },
                      {
                          "internalType": "bool",
                          "name": "isCommerce",
                          "type": "bool"
                      },
                      {
                          "internalType": "bool",
                          "name": "executed",
                          "type": "bool"
                      },
                      {
                          "internalType": "address",
                          "name": "tokenAddress",
                          "type": "address"
                      }
                  ],
                  "internalType": "struct Wallet.Transaction",
                  "name": "",
                  "type": "tuple"
              }
          ],
          "stateMutability": "view",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "enum Wallet.Role",
                  "name": "role",
                  "type": "uint8"
              },
              {
                  "internalType": "address",
                  "name": "newAddress",
                  "type": "address"
              }
          ],
          "name": "proposeAddressChange",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "inputs": [
              {
                  "internalType": "uint256",
                  "name": "_txIndex",
                  "type": "uint256"
              },
              {
                  "internalType": "address",
                  "name": "_to",
                  "type": "address"
              },
              {
                  "internalType": "uint256",
                  "name": "_value",
                  "type": "uint256"
              },
              {
                  "internalType": "bytes",
                  "name": "_data",
                  "type": "bytes"
              },
              {
                  "internalType": "bool",
                  "name": "_isCommerce",
                  "type": "bool"
              },
              {
                  "internalType": "address",
                  "name": "_tokenAddress",
                  "type": "address"
              }
          ],
          "name": "submitTransaction",
          "outputs": [],
          "stateMutability": "nonpayable",
          "type": "function"
      },
      {
          "stateMutability": "payable",
          "type": "receive"
      }
    ];
    const contract = new web3.eth.Contract(CONTRACT_ABI, ownerWalletAddress, {from: ownerAddress});
    const txIndex = randomUint256();
    const encodedSubmitABI = contract.methods.submitTransaction(txIndex, userReceiver.walletAddress, 0, encodedTransferABI, true, ERCToken.TOKEN_CONTRACT).encodeABI();
    const estimatedGas = await contract.methods.submitTransaction(txIndex, userReceiver.walletAddress, 0, encodedTransferABI, true, ERCToken.TOKEN_CONTRACT).estimateGas({
        to: contract,
        data: encodedSubmitABI,
    });
    let txObj = {
      gas : estimatedGas,
      to: ownerWalletAddress,
      value: "0x0",
      data: encodedSubmitABI,
      from: ownerAddress
    }
    const { rawTransaction } = await web3.eth.accounts.signTransaction(txObj, privateKey);
    if (!rawTransaction) {
      return toastify('Error al firmar la transaccin, recarge la pagina y vuelva a intentarlo.', 'error');
    }
    setIsShowModal(false);
    web3.eth.sendSignedTransaction(rawTransaction)
        .once('transactionHash', (function (hash) { 
            console.log(`Tx hash: ${hash}`); 
            dispatch(showLoading(false)); 
            return toastify('Transaccion realizada con xito', 'success');
        }))
        // .on('confirmation', function (confNumber, receipt) { console.log(`Confirmation: ${confNumber}`); })
        .on('error', async function (error) {
          console.log('transactionError', error);
          dispatch(showLoading(false)); 
          return toastify('Error al enviar la transaccin, recarge la pagina y vuelva a intentarlo.', 'error');
        });
  };
  // VALIDATE PIN
  if (!transferReducer) {
    return null;
  }

 

  return (
    <Cardwrapper>
      <StyledConfirmationPage>
        <HeadingContent>Transferir a</HeadingContent>
        <Cardwrapper className="profile-wrapper">
          <CardProfileUser
            avatar={userReceiver.avatar}
            username={userReceiver?.username || walletReceiver}
            wallet={userReceiver?.walletAddress}
          />
        </Cardwrapper>
        <div className="detail-item">
          <span className="text-heading-bold">Total</span>
          <span className="text-heading-bold">{moneyFormatter.format(transferReducer.amount)}</span>
          
          
        </div>
        
        <div className="btn-wrapper">
          <button onClick={actionButton} className="btn-action">
            Transferir
          </button>
        </div>
      </StyledConfirmationPage>
      <Modal showModal={isShowModal} closeModal={() => setIsShowModal(false)}>
      <Formik
            initialValues={{
              password: "",
            }}
            onSubmit={(values, { resetForm }) => {
              checkPinAction(values)
            }}
          >
      {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isValid,
            }) => (
              <StyledForm>
              <Form onSubmit={handleSubmit}>
                <div className="token-field">
                <div className="form-input">
                  <Input
                    icon="lock"
                    type={isShowPassword ? "text" : "password"}
                    showPassword={isShowPassword}
                    actionShowPassword={actionShowPassword}
                    label="password"
                    name="password"
                    placeholder="Ingrese su Contrasea"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                  />
                  {errors.password && touched.password && errors.password && (
                    <AlertValidationForm message={errors.password} />
                  )}
                </div>
                <div className="btn-wrapper">
                  <Button 
                  disabled={
                    !values.password
                  }
                  primary='primary' className="btn-action">
                    Confirmar
                  </Button>
                </div>
                </div>
              </Form>
              </StyledForm>
            )}
          </Formik>
      </Modal>
    </Cardwrapper>
  );
};

export default ConfirmationTransferPage;

const StyledForm = styled.div`

  .token-field {
    .pincode-input-container {
      display: flex;
      justify-content: space-between;
      margin-top: 50px;
      background: #ffffff;

      .pincode-input-text {
        padding: 0 !important;
        margin: 0 2px;
        text-align: center;
        border: 1px solid;
        background: transparent;
        width: 50px;
        height: 50px;
        box-shadow: 0px 10px 75px rgba(147, 147, 147, 0.1);
        /* Grey/60% */
        border: 1px solid rgba(169, 169, 169, 0.6);
        box-sizing: border-box;
        border-radius: 10px;
      }
      .pincode-input-text:focus {
        outline: none;
        box-shadow: none;
        border: 1px solid #6379f4;
      }
      .pincode-input-text:valid {
        outline: none;
        box-shadow: none;
        border: 1px solid #6379f4;
      }
    }
  }
  .btn-wrapper {
    display: flex;
    justify-content: flex-end;
    margin-top: 50px;
    .btn-action {
      width: 170px;
    }
  }
`;

const StyledConfirmationPage = styled.div`
  .detail-item {
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    margin-bottom: 1rem;
  }
  padding: 30px;
  .profile-wrapper {
    padding: 20px;
    box-sizing: content-box;
    margin-bottom: 40px;
  }
  .detail-body {
    display: flex;
    flex-direction: column;
    gap: 20px;
  }
  .btn-wrapper {
    display: flex;
    justify-content: flex-end;
    margin-top: 55px;
    .btn-action {
      width: 170px;
      ${customMedia.lessThan('603px')`
        width: 100%; 
      `}
      /* background: rgba(255, 255, 255, 0.2); */
      border: 1px solid #ffffff;
      box-sizing: border-box;
      border-radius: 10px;
      padding: 14px 27px;
      color: white;
      font: inherit;
      /* background-color: yellow; */
      background-color: ${colors.primary};
      &:hover {
        opacity: 0.8;
      }
    }
  }
`;
