import PropTypes from "prop-types";
import React from "react";
import { useHistory } from "react-router-dom";
import styled from "styled-components";
import { customMedia } from "../../Layouting/BreakPoints";
import {
  IoGridOutline,
  IoFileTrayFullOutline,
  IoLayersOutline,
  IoPersonOutline,
  IoRepeatOutline 
} from "react-icons/io5";


const MenuAsideLeft = ({ active }) => {
  console.log(active);
  const history = useHistory();

  const actionMenu = (menu) => {
      history.push(`/${menu}`);
  };

  // START = LIST OF MENU
  const listMenu = {
    transfer: "transfer",
    exchange: "exchange",
    orders: "orders",
    ads: "ads",
    // wallet: "wallet",
    tools: "tools",
    profile: "profile",
    account: "account",
    logout: "logout",
  };
  // END = LIST OF MENU

  return (
    <StyleMenuAsideLeft>
      <div className="wrapper">
        <div className="contain">
          <div className="menu-wrapper">
            <div
              className={`menu-item ${
                !Object.values(listMenu).includes(listMenu[active]) ? "active" : ""
              }`}
              onClick={() => actionMenu("dashboard")}
            >
              <div className="blox"></div>
              <div className="menu">
                <IoGridOutline className="icon"/>
                <h2 className="title-menu">Inicio</h2>
              </div>
            </div>
           
             
            {/* <div
              className={`menu-item ${
                listMenu[active] === listMenu.tools ? "active" : ""
              }`}
              onClick={() => actionMenu("qr")}
            >
              <div className="blox"></div>
              <div className="menu">
                <IoQrCodeSharp className="icon" />
                <h2 className="title-menu">Leer QR</h2>
              </div>
            </div> */}
             <div
              className={`menu-item ${
                listMenu[active] === listMenu.exchange ? "active" : ""
              }`}
              onClick={() => actionMenu("exchange")}
            >
              <div className="blox"></div>
              <div className="menu">
                <IoLayersOutline className="icon" />
                <h2 className="title-menu">Anuncios</h2>
              </div>
            </div> 
            {/* <div
              className={`menu-item ${
                listMenu[active] === listMenu.ads ? "active" : ""
              }`}
              onClick={() => actionMenu("ads")}
            >
              <div className="blox"></div>
              <div className="menu">
                <IoLayersOutline  className="icon" />
                <h2 className="title-menu">Mis Anuncios</h2>
              </div>
            </div>   */}
             <div
              className={`menu-item ${
                listMenu[active] === listMenu.account && "active"
              }`}
              onClick={() => actionMenu("account")}
            >
              <div className="blox"></div>
              <div className="menu">
                <IoPersonOutline className="icon" />
                <h2 className="title-menu">Mi Cuenta</h2>
              </div>
            </div> 
          </div>
        </div>
      </div>
    </StyleMenuAsideLeft>
  );
};

MenuAsideLeft.propTypes = {
  active: PropTypes.string,
};

export default MenuAsideLeft;

const StyleMenuAsideLeft = styled.div`
  ${customMedia.lessThan("750px")`
    width: 200px; 
  `}
  ${customMedia.lessThan("625px")`
    width: 100%;
    position: fixed;
    bottom: 0;
    left : 0;
    z-index: 999;
  `}
  height: auto;
  .rotate {
    transform: rotate(180deg);
  }

  .wrapper {
    background: #ffffff;
    box-shadow: 0px 0px 15px 1px rgb(0 0 0 / 10%);
    ${customMedia.greaterThan("625px")`
      border-radius: 25px;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
  `}
  }
  .contain {
    height: auto;
    justify-content: space-between;
    display: flex;
    ${customMedia.greaterThan("625px")`
    flex-direction: column;
  `}
    ${customMedia.lessThan("625px")`
    flex-direction: row;
    `}
  }
  .menu-wrapper {
    width: 300px;
    ${customMedia.lessThan("625px")`
    display: flex;
    align-items: center;
    justify-content: space-between;
      `}
    ${customMedia.lessThan("960px")`
      width: 100%;
    `}
    box-sizing: border-box;
    ${customMedia.greaterThan("625px")`
    padding: 30px 10px;
    padding-bottom: 0;
    `}
    ${customMedia.lessThan("625px")`
    padding: 8px 25px;
    `}
  }
  .menu-item {
    display: flex;
    gap: 1rem;
    ${customMedia.greaterThan("625px")`
     margin-bottom: 2rem;
    `}
    ${customMedia.lessThan("625px")`
      flex-direction: column-reverse;
      gap: 3px;
    `}
    .menu {
      display: flex;
      gap: 1rem;
      .icon {
        color: #55585c;
        width: 28px;
        height: 28px;
        ${customMedia.lessThan("625px")`
        width: 24px;
        height: 24px; 
      `}
      }
    }
    .title-menu {
      ${customMedia.lessThan("625px")`
        display: none; 
      `}

      margin: 0;
      font-family: Nunito Sans;
      font-style: normal;
      font-weight: normal;
      font-size: 18px;
      line-height: 31px;
      color: rgba(58, 61, 66, 0.8);
    }
    &:hover {
      cursor: pointer;
      opacity: 0.7;
    }
  }
  .menu-item.active {
    opacity: 1;
    .menu .icon {
      color: #6379F4;
    }
  }

  /* START = CONDITIONAL STYLE */
  .active {
    h2 {
      font-style: normal;
      font-weight: 700 !important;
      font-size: 18px;
      line-height: 31px;
      color: #000000;
    }
    .blox {
      width: 5px;
      background: #6379f4;
      display: block;
      ${customMedia.lessThan("620px")` 
        height: 5px;
        width: 100%;
      `}
    }
    .icon {
      path {
        stroke: #6379f4;
      }
    }
  }
`;
