import React, { useState } from 'react';
import styled from 'styled-components';

function FilterTab(props) {
    return (
        <div className="filters-tab">
            {/* <label>{props.label}</label> */}
            <select value={props.selectedValue} onChange={props.onChange}>
                <option value="">{props.label}</option>
                {props.options.map((option) => (
                    <option key={option.value} value={option.value}>
                        {option.label}
                    </option>
                ))}
            </select>
        </div>
    );
}

function Filters({info}) {
    const [selectedMoneda, setSelectedMoneda] = useState('');
    const [selectedMetodo, setSelectedMetodo] = useState('');
    const [selectedCripto, setSelectedCripto] = useState('');

    const filteredMetodos = [...new Set(info.flatMap( x => x.paymentMethod.split(',')))].map((x) => ({ label: x, value: x }));

    const filteredMonedas = Object.values(info.reduce((acc, obj) => ({ ...acc, [obj.fiatCurrency]: obj }), {})).map((x) => ({ label: x.fiatCurrency, value: x.fiatCurrency }));

    const filteredCriptos = Object.values(info.reduce((acc, obj) => ({ ...acc, [obj.currency]: obj }), {})).map((x) => ({ label: `${x?.currencyData?.symbol}` || x.currency, value: x.currency }));
    return (
        <StylesFilters>
            <div className="filters">
            <FilterTab
                    label="Cripto"
                    options={filteredCriptos}
                    selectedValue={selectedCripto}
                    onChange={(e) => {
                        setSelectedCripto(e.target.value);
                    }}
                />
                <FilterTab
                    label="Moneda"
                    options={filteredMonedas}
                    selectedValue={selectedMoneda}
                    onChange={(e) => {
                        setSelectedMoneda(e.target.value);
                    }}
                />
                <FilterTab
                    label="Pago"
                    options={filteredMetodos}
                    selectedValue={selectedMetodo}
                    onChange={(e) => {
                        setSelectedMetodo(e.target.value);
                    }}
                />
                
            </div>
        </StylesFilters>
    );
}

const StylesFilters = styled.div`
    .filters {
        display: flex;
        align-items: center;
        width: 100%;
        justify-content: flex-end;
    }
    .filters-tab {
        display: flex;
        flex-direction: column;
        margin-left: 1rem;
        label {
            line-height: 22px;
            font-size: 12px;
            color: rgba(58, 61, 66, 0.8);
        }
        select,
        input {
            border: 1px solid #e0e0e0;
            color: rgba(58, 61, 66, 0.8);
            padding: 8px 10px;
            border-radius: 8px;
        }
    }
    @media screen and (max-width: 700px) {
        .filters {
            flex-wrap: wrap;
            gap: 10px;
        }

        .filters-tab {
            margin: 0;
            width: auto;
            flex: 1;
        }
    }
`;

export default Filters;
