import React from 'react';
import styled from 'styled-components';
import PropTypes from 'prop-types';
// import {Logo} from '../../../assets/images';
import { Link } from 'react-router-dom';
import { Logo } from '../../../assets';
import { customMedia } from '../../Layouting/BreakPoints';

const logoBrand = ({ primary, light }) => {
  return (
    <Styles primary={primary} light={light}>
      <Link className="anchor" to={"/"}>
        <img src={Logo} alt='Logo'/>
        <h1>CambioVirtual</h1>
      </Link>
    </Styles>
  ); 
};

logoBrand.propTypes = {
  primary: PropTypes.string,
};

export default logoBrand;

const Styles = styled.h1`
  margin: 0;
  h1 {
    font-style: normal;
    font-weight: bold;
    font-size: 29px;
    ${customMedia.lessThan('744px')`
      font-size: 18px; 
    `}
    line-height: 40px;
    margin: 0 !important;
    color: ${({ light }) => (light ? '#ffffff' : '#1761A0')};
  }
  img {
    width: 40px; 
    ${customMedia.lessThan('744px')`
      width: 25px; 
    `}
    margin-right: 8px;
  }
  .anchor {
    text-decoration: none;
    display: flex;
    align-items: center;
    &:hover {
      opacity: 0.5;
      cursor: pointer;
    }
  }
`;
