import React from "react";
import PropTypes from "prop-types";
import styled from "styled-components";
import { colors } from "../../../utils";
import { IoArrowDownOutline, IoArrowUpOutline, IoAddOutline } from "react-icons/io5";

const Button = ({
  children,
  icon,
  onClick,
  className,
  primary,
  disabled,
  type,
}) => {
  // ICON CONDITIONAL
  const Icon = {
    transfer: <IoArrowUpOutline />,
    'top-up': <IoAddOutline />,
    withdraw: <IoArrowDownOutline />
  };
  return (
    <StyledButton
      disabled={disabled}
      className={className}
      onClick={onClick}
      primary={primary}
    >
      {Icon[icon]}
      {children}
    </StyledButton>
  );
};

Button.propTypes = {
  children: PropTypes.string.isRequired,
  icon: PropTypes.string,
  onClick: PropTypes.func,
  className: PropTypes.string,
  type: PropTypes.string,
};

Button.defaultProps = {
  children: "Type of button",
  // icon: IconArrowUp,
};

export default Button;

const StyledButton = styled.button`
  /* background: rgba(255, 255, 255, 0.2); */
  width: 100%;
  border: 1px solid #ffffff;
  box-sizing: border-box;
  border-radius: 10px;
  padding: 14px 27px;
  color: white;
  font: inherit;
  /* background-color: yellow; */
  background-color: ${({ primary }) => (primary ? colors[primary] : colors.primary)};
  background-color: ${({ disabled }) => disabled && "#DADADA"};
  color: ${({ disabled }) => disabled && "#88888F"};
  svg {
    margin-right: 1rem;
  }
  &:hover {
    opacity: ${({ disabled }) => (disabled ? "" : 0.8)};
  }
`;
