import * as bip39 from 'bip39'
import * as hdkey from 'hdkey';
import { privateToPublic, publicToAddress } from 'ethereumjs-util';
import { cipherData } from './'

export const zeroWallet = '0x0000000000000000000000000000000000000000';

export const createWallet = (cipherKey) => {
    const mnemonic = bip39.generateMnemonic(256);
    const seed = bip39.mnemonicToSeedSync(mnemonic);
    const hdwallet = hdkey.fromMasterSeed(seed);

    const path = "m/44'/60'/0'/0";
    const child = hdwallet.derive(path);

    let privateKey = child.privateKey.toString('hex');
    const publicKey = privateToPublic(Buffer.from(privateKey, 'hex')).toString('hex');
    const address = '0x' + publicToAddress(Buffer.from(publicKey, 'hex')).toString('hex');

    if (cipherKey) {
        privateKey = cipherData(cipherKey, privateKey);
    }

    return { address, privateKey };
}


