import React from 'react';
import styled from 'styled-components';
import { colors } from '../../../utils';
import { customMedia } from '../../Layouting/BreakPoints';
import { FaTelegram } from 'react-icons/fa';

const Footer = () => {
  return (
    <StyledFooter>
      <div className="custom-container">
        
        <div className="right">
          <a target='_blank' href='https://t.me/CambioVirtual' className='telegram-wrap' rel="noreferrer">
            <FaTelegram className="telegram-icon" />
            <p className="text-regular telegram">CambioVirtual</p>
          </a> 
        </div>
        <div className="left">
          <p className="text-light"> {new Date().getFullYear()} CambioVirtual.com </p>
        </div>
      </div>
    </StyledFooter>
  );
};

export default Footer;

const StyledFooter = styled.footer`
  background-color: ${colors.primary};
  padding: 20px 0;
  .telegram-icon {
    width: 25px;
    height: auto;
    color: white;
    margin-right: 8px;
    ${customMedia.lessThan('744px')`
    width: 16px;
    `}
  }
  .telegram-wrap {
    display: flex;
    align-items: center;
    justify-content: center;
    text-decoration: none;
  }
  margin-top: 60px;
  .text-light {
    font-size: 12px;
    ${customMedia.lessThan('744px')`
      text-align: right; 
    `}
    ${customMedia.greaterThan('447px')`
      font-size: 15px;
    `}
  }
  .text-regular {
    
    white-space: nowrap;
  }
  p {
    color: white;
    margin: 0;
  }
  .custom-container {
    width: 100%;
    ${customMedia.lessThan('744px')`
    padding: 0 30px;
    `}
    ${customMedia.greaterThan('744px')`
    padding: 0 50px;
    `}
    flex-direction: row-reverse;
    ${customMedia.lessThan('mobile')`
    padding-bottom: 50px;
   `}
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
    ${customMedia.lessThan('744px')`
      flex-direction: column; 
    `}
    .left {
      
    }
    .right {
      display: flex;
      justify-content: space-between;
      font-size: 15px;
      ${customMedia.lessThan('447px')`
      flex-direction: column; 
      align-items: flex-end;
      margin-bottom: 8px;
    `}
    ${customMedia.greaterThan('447px')`
      gap: 40px;
    `}
    }
  }
`;
