import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'react-redux';
import { Cardwrapper, HeadingContent } from '../../components';
import QRCode from 'qrcode.react';

const TopupPage = () => {
  const userState = useSelector((state) => state.userReducer.data);
  const { wallets } = userState;
  const publicKey = wallets?.[0]?.publicKey;
  
  return (
    <Cardwrapper>
      <StyledTopupPage>
        <HeadingContent>Como Ingresar Dinero</HeadingContent>
        <div className="body-content">
          <Cardwrapper className="item">
            <p className="text-regular">
              Enva <b>TetherUSD (USDT)</b> de la red <b>Polygon</b> a esta direccin
            </p>
          </Cardwrapper>
          <div className="item d-flex flex-column align-items-center justify-content-center">
            <p className="text-regular publicKey"><b>{publicKey}</b></p>
            <QRCode value={publicKey} size={200} />
          </div>
        </div>
      </StyledTopupPage>
    </Cardwrapper>
  );
};

export default TopupPage;

const StyledTopupPage = styled.div`
  padding: 30px;
  .body-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .item {
      padding: 30px 20px;
      display: flex;
      gap: 1rem;
      p {
        margin-bottom: 0;
        overflow: auto
      }
      .number {
        font-family: 'Nunito Sans';
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #6379f4;
      }
      .publicKey {
        font-size: 15px;
        word-break: break-all;
        margin-bottom: 1rem;
      }
    }
  }
`;
