import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Cardwrapper } from '../../components';
import { CambioVirtualJSON, ERC20ABI, createAd, hexaToDecimal } from '../../utils';
import detectEthereumProvider from '@metamask/detect-provider';
import Web3 from 'web3';
import { ContractsList } from '../../config/config';
import { dispatchTypes } from '../../utils/dispatchType';
import { useDispatch } from 'react-redux';
import Button from 'react-bootstrap/Button';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { useHistory } from 'react-router-dom/cjs/react-router-dom'; 
import { zeroAddress } from 'ethereumjs-util';

const CreateAdPage = () => {
    const [contract, setContract] = useState(null);
    const [activeAds, setActiveAds] = useState([]);
    const history = useHistory();
    const initialState = { accounts: [], balance: '', chainId: '' };
    const [wallet, setWallet] = useState(initialState);
    const dispatch = useDispatch();
    const [createAdForm, setCreateAdForm] = useState({});

    const handleInputChange = (event) => {
        const value = event.target.value;
        const key = event.target.name;
        setCreateAdForm((state) => ({...state, [key]: value}));
      };

    const getCurrency = async (address) => {
        const isOnList = ContractsList?.[wallet?.chainId]?.[address];
        if (isOnList) {
            return isOnList;
        }
        const web3Instance = new Web3(window.ethereum);
        const tokenContract = new web3Instance.eth.Contract(ERC20ABI, address);
        const name = await tokenContract.methods?.name?.()?.call();
        const symbol = await tokenContract.methods?.symbol?.()?.call();
        const decimals = await tokenContract.methods?.decimals?.()?.call();
        const balanceOf = await tokenContract.methods?.balanceOf?.(wallet.accounts[0])?.call();

        return { name, symbol, decimals, balanceOf };
    };

    useEffect(() => {
        const refreshAccounts = (accounts) => {
            if (accounts?.length > 0) {
                updateWallet(accounts);
            } else {
                setWallet(initialState);
            }
        };

        const refreshChain = async (chainId) => {
            setWallet((wallet) => ({ ...wallet, chainId }));
            const accounts = await window.ethereum.request({ method: 'eth_accounts' });
            refreshAccounts(accounts);
        };

        const getProvider = async () => {
            const provider = await detectEthereumProvider({ silent: true });

            if (provider) {
                const accounts = await window.ethereum.request({ method: 'eth_accounts' });

                if (accounts[0] !== wallet.accounts[0]) refreshAccounts(accounts);
                window.ethereum.on('accountsChanged', refreshAccounts);
                window.ethereum.on('chainChanged', refreshChain);
            }
        };

        getProvider();

        return () => {
            window.ethereum?.removeListener('accountsChanged', refreshAccounts);
            window.ethereum?.removeListener('chainChanged', refreshChain);
        };
        // eslint-disable-next-line
    }, [wallet]);
    useEffect(() => {
        const loadActiveAds = async () => {
            if (contract) {
                try {
                    const ads = await contract.methods.getActiveAds().call();
                    let adsInfo = [];
                    for (let item of ads) {
                        const currencyData = await getCurrency(item.currency);
                        adsInfo.push({ ...item, currencyData });
                    }
                    setActiveAds(adsInfo);
                } catch (error) {
                    console.error('Error while loading active ads:', error);
                }
            }
        };

        loadActiveAds();
        // eslint-disable-next-line
    }, [contract]);

    useEffect(() => {
        const web3Instance = new Web3(window.ethereum);
        const networkId = wallet.chainId;
        const deployedNetwork = CambioVirtualJSON.networks[networkId];
        if (deployedNetwork?.address) {
            const escrowContract = new web3Instance.eth.Contract(CambioVirtualJSON.abi, deployedNetwork.address);
            setContract(escrowContract);
        } else {
            setActiveAds([]);
        }
    }, [wallet]);

    const updateWallet = async (accounts) => {
        const balance = await window.ethereum.request({
            method: 'eth_getBalance',
            params: [accounts[0], 'latest']
        });
        const chainId = await window.ethereum.request({
            method: 'eth_chainId'
        });
        const newWallet = { accounts, balance: hexaToDecimal(balance), chainId: hexaToDecimal(chainId) };
        setWallet(newWallet);
        dispatch({ type: dispatchTypes.setWalletAccount, payload: newWallet });
    };

    const formatAmount = (amount, decimals) => {
        let resultado = (amount / 10 ** decimals).toFixed(decimals);
        return resultado.startsWith('0.') ? resultado.replace(/\.?0+$/, '') : parseFloat(resultado).toFixed(6);
    };

    const handleSubmit = async () => {
        let ad = Object.assign({},createAdForm);
        for(let item in ad) {
            if (['fiatMinValue', 'fiatPricePerToken'].includes(item)) {
                ad[item] = (ad[item] * 10000).toString()
            }
            if (item === 'amount') {
                ad[item] = (ad[item] * (10 ** 18)).toString()
            }
            if (item === 'isSeller') {
                ad[item] = ad[item] === 'true';
            }
        }
        console.log(ad);
        await createAd(ad.user, ad.isSeller, ad.amount, ad.currency, ad.fiatPricePerToken, ad.fiatCurrency, ad.paymentMethod, ad.fiatMinValue, ad.expirationTime);
    }

    return (
        <>
            <Cardwrapper>
                <StyledPage>
                    {/* <HeadingContent>Exchange</HeadingContent> */}
                    <div className="body-content">
                        <div className="balance-wrapper">
                            <h3 className="text-section"> Wallet ID</h3>
                            <div className="d-flex align-items-center justify-content-between" style={{overflow: 'auto'}}>
                                <a onClick={() => history.push(`/${wallet.accounts[0]}`)} className="public-link" target="_blank" rel="noreferrer">
                                    {wallet.accounts[0]}
                                </a>
                            </div>
                        </div>
                        <hr></hr>
                        <h2>Mis Anuncios</h2>
                        {activeAds
                            .filter((x) => x?.owner?.toLowerCase() === wallet?.accounts[0]?.toLowerCase())
                            ?.map?.((ad) => (
                                <div className="offer-info" key={ad.adId}>
                                    <span>
                                        #{ad.adId} <b>{ad.isSeller ? 'Venta' : 'Compra'}</b>
                                    </span>
                                    <span>
                                        {`${formatAmount(ad.amount, parseInt(ad.currencyData.decimals) || 18)}`} {ad.currencyData.symbol || ad.currency}
                                    </span>
                                    <span>
                                        <b>
                                            ${ad.fiatPricePerToken} {ad.fiatCurrency}
                                        </b>
                                    </span>
                                    <span>{ad.paymentMethod}</span>
                                    <button>Modificar</button>
                                    <button>Detener</button>
                                </div>
                            ))}
                        <hr></hr>
                        <h2>Crear Anuuncio</h2>
                        <Container>
                          <Row>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Tipo de Anuncio</Form.Label>
                                <Form.Select onChange={handleInputChange} name='isSeller'>
                                    <option value="">Seleccione el tipo de anuncio</option>
                                    <option value="false">Compra</option>
                                    <option value="true">Venta</option>
                                </Form.Select>
                            </Form.Group></Col>
                            <Col xs={9}>
                            <Form.Group className="mb-3">
                                <Form.Label>Direccin de la Criptomoneda</Form.Label>
                                <Form.Control type="text" placeholder="0x..." onChange={handleInputChange} name='currency' value={createAdForm?.currency || ''}/>
                                <Form.Text className="text-muted">Dejar vaco para moneda nativa.</Form.Text>
                            </Form.Group>
                            </Col>
                          </Row>
                          <Row>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Usuario de Telegram</Form.Label>
                                <Form.Control type="text" placeholder="t.me/..." onChange={handleInputChange} name='user' value={createAdForm?.user || ''}/>
                            </Form.Group></Col>
                            <Col><Form.Group className="mb-3">
                                <Form.Label>Monto</Form.Label>
                                <Form.Control type="number" placeholder="0.0" onChange={handleInputChange} name='amount' value={createAdForm?.amount || ''}/>
                            </Form.Group></Col>
                            
                          </Row>
                          <Row>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Moneda Fiat</Form.Label>
                                <Form.Select className="mb-3" onChange={handleInputChange} name='fiatCurrency' value={createAdForm?.fiatCurrency || ''}>
                                    <option value="">Seleccione Moneda</option>
                                    <option value="USD">USD</option>
                                    <option value="EUR">EUR</option>
                                    <option value="ARS">ARS</option>
                                    <option value="UYU">UYU</option>
                                </Form.Select>
                                {/* <Form.Control type="text" placeholder="Otra"/> */}
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Metodos de Pago</Form.Label>
                                {/* <Form.Select multiple className="mb-3" onChange={handleInputChange} name='paymentMethod'>
                                    <option value='Banco Santander Argentina'>Banco Santander Argentina</option>
                                    <option value='Mercado Pago'>Mercado Pago</option>
                                    <option value='Lemon Cash'>Lemon Cash</option>
                                    <option value='Banco Itau Uruguay'>Banco Ita Uruguay</option>
                                    <option value='PayPal'>PayPal</option>
                                    <option value='Zelle'>Zelle</option>
                                    <option value='Payoneer'>Payoneer</option>
                                </Form.Select> */}
                                <Form.Control type="text" onChange={handleInputChange} name='paymentMethod' value={createAdForm?.paymentMethod || ''}/> 
                                <Form.Text className="text-muted">Separados por coma</Form.Text> 
                            </Form.Group>
                            </Col>
                            
                          </Row>
                          <Row>
                          <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Precio Fiat Por Unidad</Form.Label>
                                <Form.Control type="number" placeholder="0.0" onChange={handleInputChange} name='fiatPricePerToken' value={createAdForm?.fiatPricePerToken || ''}/>
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Valor Minimo de Orden</Form.Label>
                                <Form.Control type="number" placeholder="0.0" onChange={handleInputChange} name='fiatMinValue' value={createAdForm?.fiatMinValue || ''}/>
                            </Form.Group>
                            </Col>
                            <Col>
                            <Form.Group className="mb-3">
                                <Form.Label>Tiempo de Espera</Form.Label>
                                <Form.Control type="number" placeholder="0" onChange={handleInputChange} name='expirationTime' value={createAdForm?.expirationTime || ''}/>
                                <Form.Text className="text-muted">En segundos</Form.Text>
                            </Form.Group></Col>
                     
                            
                          </Row>
                        </Container>

                        
                            
                            
                            
                            <Button variant="primary" type="button" onClick={handleSubmit}>
                                Crear Oferta
                            </Button>
                        
                    </div>
                </StyledPage>
            </Cardwrapper>
        </>
    );
};

export default CreateAdPage;

const StyledPage = styled.div`
    padding: 50px 10px;
    .widget {
        max-width: 100%;
    }
    .body-content {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        .item {
            padding: 30px 20px;
            display: flex;
            gap: 1rem;
            p {
                margin-bottom: 0;
            }
            .number {
                font-family: Nunito Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 25px;
                color: #6379f4;
            }
        }
    }
`;
