import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { Button, Cardwrapper, HeadingContent } from '../../components';
import { call, send, OrderStatusEnum, OrderStatusIcon, compareAddresses } from '../../utils';

const OrdersPage = () => {
    const [orders, setOrders] = useState([]);
    const [account, setAccount] = useState('');

    useEffect(() => {
        const handleConnect = async () => {
            let accounts = await window.ethereum.request({
                method: 'eth_requestAccounts'
            });
            setAccount(accounts[0]);
            await callOrderList(accounts[0]);
        };

        handleConnect();
        // eslint-disable-next-line
    }, []);

    const callOrderList = async (address) => {
        let ordersList = await call('getOrdersByAddress', [address]);
        const promises = ordersList.map(async (order) => {
            const ad = await call('ads', [order.adId]);
            return { ...order, ad };
        });
        ordersList = await Promise.all(promises);
        console.log(ordersList);
        setOrders(ordersList);
    };
    console.log(account);

    const handleOrderAction = async (action, orderId) => {
        try {
            const result = await send(action, [orderId]);
            console.log(result);
            await callOrderList(account);
        } catch (error) {
            console.log(error);
        }
    };

    return (
        <Cardwrapper>
            <StyledPage>
                <HeadingContent>Mis rdenes</HeadingContent>
                <div className="body-content">
                    {orders?.length > 0 ? (
                        orders.map((order) => (
                            <StyledOrder key={order.orderId}>
                                <div>
                                    <span className="order-status">
                                        {OrderStatusIcon[order.status]} <b>{OrderStatusEnum[order.status]}</b>
                                    </span>
                                    <span className="last-update">{new Date(Number(order.updatedAt) * 1000).toLocaleString()}</span>
                                </div>
                                <div>
                                    <span>{order.value}</span>
                                    <span>{order.currency}</span>
                                </div>
                                <div>
                                    <span>{new Date(Number(order.createdAt) * 1000).toLocaleString()}</span>
                                </div>
                                <div>
                                    {compareAddresses(account, order.receiver) && ['0', '2'].includes(order.status) ? (
                                        <Button onClick={() => handleOrderAction('fiatTransfered', order.orderId)}>Dinero Transferido</Button>
                                    ) : null}
                                    {(compareAddresses(account, order.receiver) && ['0', '1', '2'].includes(order.status)) ||
                                    (compareAddresses(account, order.sender) &&
                                        order.status === '0' &&
                                        (new Date() - new Date(Number(order.createdAt) * 1000)) / (1000 * 60) > 30) ? (
                                        <Button onClick={() => handleOrderAction('cancelOrder', order.orderId)}>Cancelar</Button>
                                    ) : null}
                                    {compareAddresses(account, order.sender) && ['1', '2'].includes(order.status) ? (
                                        <Button onClick={() => handleOrderAction('releaseOrder', order.orderId)}>He recibido el dinero</Button>
                                    ) : null}

                                    {order.status === '1' && (new Date() - new Date(Number(order.createdAt) * 1000)) / 1000 > order.ad.expirationTime ? <Button>Abrir Disputa</Button> : null}
                                </div>
                            </StyledOrder>
                        ))
                    ) : (
                        <span>No tienes rdenes activas</span>
                    )}
                </div>
            </StyledPage>
        </Cardwrapper>
    );
};
{
    /* <div className='table-headers'>
                                <span>Ad</span>
                                <span>Orden</span>
                                <span>Fecha de Inicio</span>
                                <span>Estado</span>
                                <span>Usuario</span>
                                <span>Enva</span>
                                <span>Recibe</span>
                                <span>Moneda</span>
                                <span>Monto</span>
                                <span>ltima Actualizacin</span>
                            </div>  <span>#{order.adId}</span>
                                    <span>#{order.orderId}</span>
                                    <span>{order.createdAt}</span>
                                    <span>{order.status}</span>
                                    <span>{order.user}</span>
                                    <span>{order.sender}</span>
                                    <span>{order.receiver}</span>
                                    <span>{order.currency}</span>
                                    <span>{order.value}</span>
                                    <span>{order.updatedAt}</span> */
}
export default OrdersPage;

const StyledOrder = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #e0e0e0;
    border-radius: 8px;
    padding: 10px 15px;
    overflow: auto;
    div {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .order-status {
        display: flex;
        align-items: center;
        font-size: 15px;
    }

    .order-status b {
        font-size: 18px;
        margin-left: 8px;
    }

    .last-update {
        color: #bdbdbd;
        font-size: 14px;
    }

    .table-headers {
        width: 100%;
        justify-content: space-between;
    }
`;

const StyledPage = styled.div`
    padding: 30px;
    .body-content {
        display: flex;
        flex-direction: column;
        gap: 1rem;
        .item {
            padding: 30px 20px;
            display: flex;
            gap: 1rem;
            p {
                margin-bottom: 0;
                overflow: auto;
            }
            .number {
                font-family: Nunito Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 25px;
                color: #6379f4;
            }
            .publicKey {
                font-size: 15px;
                word-break: break-all;
                margin-bottom: 1rem;
            }
        }
    }
`;
