import { IoShieldCheckmark, IoLogoUsd, IoReaderOutline } from 'react-icons/io5';

export const patternEmail = /^(([^<>()[\]\\.,;:\s@"]+(\.[^<>()[\]\\.,;:\s@"]+)*)|(".+"))@((\[[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\.[0-9]{1,3}\])|(([a-zA-Z\-0-9]+\.)+[a-zA-Z]{2,}))$/;

export const phoneRegExp = /^((\\+[1-9]{1,4}[ \\-]*)|(\\([0-9]{2,3}\\)[ \\-]*)|([0-9]{2,4})[ \\-]*)*?[0-9]{3,4}?[ \\-]*[0-9]{3,4}?$/;

export const moneyFormatter = new Intl.NumberFormat('en', { style: 'currency', currency: 'USD', minimumFractionDigits: 3 });

export const objectToUriParams = (obj) => {
    return Object.keys(obj)
        .map((k) => encodeURIComponent(k) + '=' + encodeURIComponent(obj[k]))
        .join('&');
};

export const isAddress = (string) => {
    return string?.length === 42 && string.startsWith('0x');
};

export const shortAddress = (address, length = 8) => {
    address = address.toLowerCase();
    const prefix = address.substring(0, length);
    const suffix = address.substring(address.length - length + 2);
    return `${prefix}...${suffix}`;
};

export const compareAddresses = (addr1, addr2) => {
    return isAddress(addr1) && isAddress(addr2) && addr1.toLowerCase() === addr2.toLowerCase();
};

export const hexaToDecimal = (hexString) => {
    if (!hexString) return '';
    return parseInt(hexString.replace(/^0x/, ''), 16);
};

export const OrderStatusEnum = {
    0: 'Criptos en Custodia',
    1: 'Dinero Transferido',
    2: 'Disputa'
};

export const OrderStatusIcon = {
    0: <IoShieldCheckmark />,
    1: <IoLogoUsd />,
    2: <IoReaderOutline />
};
