import { Form, Formik } from 'formik';
import React, { useState } from 'react';
import { useDispatch } from 'react-redux';
import { Link, useHistory } from 'react-router-dom';
import styled from 'styled-components';
import * as Yup from 'yup';
import {
  AlertValidationForm,
  Button,
  Input,
  SidebarAuth,
} from '../../components';
import { customMedia } from '../../components/Layouting/BreakPoints';
import { registerUser } from '../../config/Redux/actions/userActions';

const SignupPage = () => {
  const dispatch = useDispatch();
  const history = useHistory();
  const validate = Yup.object({
    username: Yup.string().required('Se requiere el Nombre de Usuario'),
    email: Yup.string().email('Email no es vlido').required('Email es Requerido'),
    password: Yup.string()
      .min(8, 'Contrasea debe tener al menos 8 caractres')
      .required('Contrasea es Requerida'),
  });

  // START = ACTION SHOW PASSWORD
  const [isShowPassword, setIsShowPassword] = useState(false);

  const actionShowPassword = () => {
    isShowPassword ? setIsShowPassword(false) : setIsShowPassword(true);
  };
  // END = ACTION SHOW PASSWORD


  return (
    <Styles>
      <div className="row-side">
        <SidebarAuth />
      </div>
      <div className="form-side">
        <div className="content">
        <h5>
            Comiense a Administrar su Dinero
            <br />
            Desde todas sus Plataformas Favoritas
            <br />
            Con 10,000+ Usuarios
            <br />
          </h5>
          <h6>
            Transfiere Dinero a tus Amigos <br />
            desde Wallet estes donde estes. Disponible en App y Web
            <br />
            Sin comisiones!
          </h6>
          <Formik
            initialValues={{
              username: '',
              email: '',
              password: '',
            }}
            validationSchema={validate}
            onSubmit={(values, { resetForm }) => {
              // console.log(values);
              dispatch(registerUser(values, history));
              resetForm();
            }}
          >
            {({
              values,
              errors,
              touched,
              handleChange,
              handleBlur,
              handleSubmit,
              isValid,
            }) => (
              <Form onSubmit={handleSubmit}>
                <div className="form-input">
                  <Input
                    icon="person"
                    type="text"
                    name="username"
                    label="username"
                    placeholder="Ingrese su Nombre de Usuario"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.username}
                  />
                  {errors.username && touched.username && errors.username && (
                    <AlertValidationForm message={errors.username} />
                  )}
                </div>
                <div className="form-input">
                  <Input
                    icon="mail"
                    type="text"
                    name="email"
                    label="email"
                    placeholder="Ingrese su Email"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.email}
                  />
                  {errors.email && touched.email && errors.email && (
                    <AlertValidationForm message={errors.email} />
                  )}
                </div>
                <div className="form-input">
                  <Input
                    icon="lock"
                    type={isShowPassword ? 'text' : 'password'}
                    showPassword={isShowPassword}
                    actionShowPassword={actionShowPassword}
                    label="password"
                    name="password"
                    placeholder="Escriba una Contrasea"
                    onChange={handleChange}
                    onBlur={handleBlur}
                    value={values.password}
                  />
                  {errors.password && touched.password && errors.password && (
                    <AlertValidationForm message={errors.password} />
                  )}
                </div>
                <Button
                  primary
                  disabled={
                    !isValid ||
                    (Object.keys(touched).length === 0 &&
                      touched.constructor === Object)
                  }
                  className="button-login"
                  type="submit"
                >
                  Continuar
                </Button>
              </Form>
            )}
          </Formik>
          <div className="register-link">
            Ya tiene una Cuenta ? {' '}
            <Link
              to="/login"
              className="link"
              style={{ textDecoration: 'none' }}
            >
              {' '}
              Ingrese Aqui{' '}
            </Link>
          </div>
        </div>
      </div>
    </Styles>
  );
};

export default SignupPage;
const Styles = styled.div`
  height: 100vh;
  display: flex;
  ${customMedia.lessThan('tablet')`
  flex-direction: column
  `}
  /* background: orange; */

  .row-side {
    /* height: 100%; */
    width: 55%;
    ${customMedia.lessThan('tablet')`
 width: 100%;
  `}
  ${customMedia.lessThan('mobile')`
    display: none;
   `}
  }
  .sidebar {
    width: 100%;
  }

  .form-side {
    /* width: 50%; */
    width: 45%;
    display: flex;
    justify-content: center;
    align-items: center;
    ${customMedia.lessThan('tablet')` 
      width: 100%;
    `}
    ${customMedia.lessThan('mobile')`
    padding: 30px 0;
   `}
    .content {
      width: 80%;
      h5 {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 24px;
        line-height: 33px;
        color: #3a3d42;
      }
      h6 {
        font-style: normal;
        margin-bottom: 0;
        margin-top: 20px;
        font-weight: normal;
        font-size: 16px;
        line-height: 30px;
        color: rgba(58, 61, 66, 0.6);
      }
      form {
        margin-top: 40px;
        .form-input {
          width: 100%;
          margin-bottom: 30px;
        }
        .forgot-pass {
          margin-top: 20px;
          font-style: normal;
          font-weight: 600;
          font-size: 14px;
          text-align: right;
          &:hover {
            opacity: 0.5;
          }
        }
        .button-login {
          margin-top: 40px;
        }
      }
    }
  }
  .register-link {
    margin-top: 20px;
    font-weight: 600;
    display: flex;
    gap: 5px;
    justify-content: center;
    p {
      margin-bottom: 0;
    }
  }
`;
