import React, { useState, useEffect } from 'react';
import { useSelector } from 'react-redux';
import styled from 'styled-components';
import { LogoBrand } from '../../atoms';
import { customMedia } from '../../Layouting/BreakPoints';
import NotificationPopup from '../NotificationPopup';
import NotificationsNoneIcon from '@material-ui/icons/NotificationsNone';

const Navbar = () => {
    const userState = useSelector((state) => state.userReducer.data);
    console.log('USER', userState);
    const { wallet } = userState;
    const [setNotification, setShowNotification] = useState(false);
    const [selectedChainId, setSelectedChainId] = useState(wallet?.hexaChainId);

    const handleChange = async (event) => {
        try {
            await window.ethereum.request({
                method: 'wallet_switchEthereumChain',
                params: [{ chainId: `${event.target.value.toString(16)}` }]
            });
            setSelectedChainId(event.target.value);
        } catch (error) {
            setSelectedChainId(selectedChainId);
        }
    };

    useEffect(() => {
        const getCurrentChainId = async () => {
            const chainId = await window.ethereum.request({
                method: 'eth_chainId'
            });
            console.log('chainId', chainId);
            setSelectedChainId(chainId);
        };
        getCurrentChainId();
        // eslint-disable-next-line
    }, []);
    console.log('selectedChainId', selectedChainId);
    return (
        <NavbarStyling>
            <div className="custom-container">
                <LogoBrand />
                <div className="navigation-user-right">
                    {/* <select value={selectedChainId} onChange={handleChange}>
                        <option value="0x13881">Polygon Testnet</option>
                        <option value="0x5">Ethereum Testnet</option>
                        <option value="0x61">BSC Testnet</option>
                    </select> */}
                    <div className="icon-wrapper" onClick={() => setShowNotification(true)}>
                        <NotificationsNoneIcon fontSize="inherit" color="inherit" />
                    </div>
                </div>
            </div>
            <NotificationPopup showPopup={setNotification} closePopup={() => setShowNotification(false)} />
        </NavbarStyling>
    );
};

Navbar.propTypes = {};

export default Navbar;

const NavbarStyling = styled.nav`
    background: #ffffff;
    box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.05);
    border-radius: 0;
    padding: 12px 0;
    position: fixed;
    width: 100%;
    top: 0;
    z-index: 99;
    .custom-container {
        position: relative;
        ${customMedia.lessThan('1000px')`
            width: 90%; 
        `}
        width: 100%;
        ${customMedia.greaterThan('744px')`
            padding: 0 50px;
        `}
        margin: 0 auto;
        display: flex;
        justify-content: space-between;
        align-items: center;
        ${customMedia.lessThan('744px')`
        `}
        .navigation-user-right {
            display: flex;
            align-items: center;
            gap: 10px;
            .user-profile {
                display: flex;
                gap: 1rem;
                flex-direction: row;
                &:hover {
                    opacity: 0.7;
                    cursor: pointer;
                }
                span {
                    align-items: center;
                    display: flex;
                }
            }
            .image-wrapper {
                width: 40px;
                height: 40px;

                img {
                    width: 100%;
                    height: 100%;
                    object-fit: cover;
                    border: 2px solid #e5e8ed;
                    border-radius: 50%;
                }

                &:hover {
                    opacity: 0.8;
                    cursor: pointer;
                }
            }
            .image-wrapper img:hover {
                border-color: #6379f4;
            }
            .profile-identity {
                display: flex;
                justify-content: center;
                flex-direction: column;
                ${customMedia.lessThan('430px')`
            display: none; 
        `}
                .username {
                    font-family: Nunito Sans;
                    font-style: normal;
                    font-weight: bold;
                    font-size: 18px;
                    line-height: 31px;
                    text-align: center;
                    color: #3a3d42;
                    margin: 0;
                }
                .phone-number {
                    font-family: Nunito Sans;
                    font-style: normal;
                    font-weight: normal;
                    font-size: 13px;
                    line-height: 24px;
                    color: rgba(58, 61, 66, 0.9);
                    margin: 0;
                }
            }
            .icon-wrapper {
                display: flex;
                font-size: 25px;
                color: #e5e8ed;
                border-radius: 50%;
                padding: 6.5px 6.5px 6.5px 6.5px;
                &:hover {
                    color: #6379f4;
                    opacity: 0.5;
                    cursor: pointer;
                }
            }
        }
    }
`;
