import React, { useEffect, useState } from 'react';
import { Link, useHistory } from 'react-router-dom';
import styled from 'styled-components';
import Button from '../../components/atoms/Button';
import { moneyFormatter, hexaToDecimal, ERC20ABI, CambioVirtualJSON, call, send, OrderStatusEnum, OrderStatusIcon, compareAddresses } from '../../utils';
import { StyledDashboard } from './styled';
import detectEthereumProvider from '@metamask/detect-provider';
import { Cardwrapper, HexToColorVector } from '../../components';
import { ChainList, ContractsList } from '../../config/config';
import Web3 from 'web3';

const Homepage = () => {
    const history = useHistory();

    const initialState = { accounts: [], balance: '', chainId: '' };
    const [wallet, setWallet] = useState(initialState);
    const [deployedAddress, setDeployedAddress] = useState('');
    const [balances, setBalances] = useState([]);
    const [ads, setAds] = useState([]);
    const [orders, setOrders] = useState([]);
    const [shares, setShares] = useState({ balanceOf: '0', name: 'CambioVirtual Shares', symbol: 'CV Shares', token: '' });

    const refreshAccounts = (accounts) => {
        if (accounts.length > 0) {
            updateWallet(accounts);
        } else {
            // if length 0, user is disconnected
            setWallet(initialState);
        }
    };

    const refreshChain = (chainId) => {
        setWallet((wallet) => ({ ...wallet, chainId }));
        const deployedNetwork = CambioVirtualJSON.networks[hexaToDecimal(chainId)];
        if (deployedNetwork?.address) {
            setDeployedAddress(deployedNetwork?.address);
        }
    };

    const getProvider = async () => {
        const accounts = await window.ethereum.request({ method: 'eth_accounts' });
        refreshAccounts(accounts);
        window.ethereum.on('accountsChanged', refreshAccounts);
        window.ethereum.on('chainChanged', refreshChain);
    };

    useEffect(() => {
        getProvider();

        return () => {
            window.ethereum?.removeListener('accountsChanged', refreshAccounts);
            window.ethereum?.removeListener('chainChanged', refreshChain);
        };
    }, []);

    const updateWallet = async (accounts) => {
        const balance = await window.ethereum.request({
            method: 'eth_getBalance',
            params: [accounts[0], 'latest']
        });
        const chainId = await window.ethereum.request({
            method: 'eth_chainId'
        });
        setWallet({ accounts, balance, chainId });
        const deployedNetwork = CambioVirtualJSON.networks[hexaToDecimal(chainId)];
        if (deployedNetwork?.address) {
            setDeployedAddress(deployedNetwork?.address);
        }
    };

    const formatAmount = (amount, decimals) => {
        let resultado = (amount / 10 ** decimals).toFixed(decimals);
        return resultado.startsWith('0.') ? resultado.replace(/\.?0+$/, '') : parseFloat(resultado).toFixed(6);
    };

    useEffect(() => {
        window.scrollTo(0, 0);
    }, []);

    const getCurrency = async (address, withBalance = false) => {
        const isOnList = ContractsList?.[hexaToDecimal(wallet?.chainId)]?.[address];
        if (isOnList) {
            return isOnList;
        }
        const web3Instance = new Web3(window.ethereum);
        const tokenContract = new web3Instance.eth.Contract(ERC20ABI, address);
        if (!tokenContract) {
            return {};
        }
        const name = await tokenContract.methods?.name?.()?.call();
        const symbol = await tokenContract.methods?.symbol?.()?.call();
        const decimals = await tokenContract.methods?.decimals?.()?.call();
        const balanceOf = withBalance && wallet?.accounts?.[0] ? await tokenContract.methods?.balanceOf?.(wallet.accounts[0])?.call() : 0;

        return { name, symbol, decimals, balanceOf };
    };

    const handleConnect = async () => {
        if (wallet?.accounts?.[0]) {
            await callBalance();
            const ownerToken = await call('ownerToken');
            const ownerTokenData = await getCurrency(ownerToken, true);
            setShares(() => ({ token: ownerToken, ...ownerTokenData }));
            let adsCall = await call('getAdsByAddress', [wallet.accounts[0]]);
            if (Array.isArray(adsCall) && adsCall?.length > 0) {
                const promises = adsCall.map(async (item) => {
                    const currencyData = ContractsList?.[hexaToDecimal(wallet?.chainId)]?.[item.currency] && (await getCurrency(item.currency));
                    return { ...item, currencyData };
                });
                adsCall = await Promise.all(promises);
            }
            setAds(() => adsCall);

            let ordersCall = await call('getOrdersByAddress', [wallet.accounts[0]]);
            if (Array.isArray(adsCall) && adsCall?.length > 0) {
                const promises = ordersCall.map(async (order) => {
                    const adFromState = ads.find((ad) => ad?.adId === order.adId);
                    if (adFromState) {
                        return { ...order, ad: adFromState };
                    }
                    const ad = await call('ads', [order.adId]);
                    return { ...order, ad };
                });
                ordersCall = await Promise.all(promises);
            }
            setOrders(() => ordersCall);
        } else {
            let accounts = await window.ethereum.request({
                method: 'eth_requestAccounts'
            });
            updateWallet(accounts);
        }
    };

    useEffect(() => {
        handleConnect();
        // eslint-disable-next-line
    }, [wallet]);

    const callBalance = async () => {
        let balanceList = await call('getMyBalance');
        if (Array.isArray(balanceList) && balanceList?.length > 0) {
            const promises = balanceList.map(async (item) => {
                const currencyData = await getCurrency(item.currency);
                return { ...item, currencyData };
            });
            balanceList = await Promise.all(promises);
        }
        setBalances(() => balanceList);
    };

    const handleOrderAction = async (action, orderId) => {
        try {
            const result = await send(action, [orderId]);
            console.log(result);
            await callOrderList(wallet?.accounts?.[0]);
        } catch (error) {
            console.log(error);
        }
    };

    const handleClaim = async (currency) => {
        const result = await send('claim', [currency]);
        console.log(result);
    }

    const handleAdAction = async (action, adId) => {
        try {
            const result = await send(action, [adId]);
            console.log(result);
            handleConnect();
        } catch (error) {
            console.log(error);
        }
    };

    const callOrderList = async (address) => {
        let ordersList = await call('getOrdersByAddress', [address]);
        const promises = ordersList.map(async (order) => {
            const adFromState = ads.find((ad) => ad?.adId === order.adId);
            if (adFromState) {
                return { ...order, ad: adFromState };
            }
            const ad = await call('ads', [order.adId]);
            return { ...order, ad };
        });
        ordersList = await Promise.all(promises);
        setOrders(ordersList);
    };

    return (
        <StyledDashboard>
            <div className="middle">
                <div className="header-section">
                    <div className="balance-wrapper">
                        <h3 className="text-section">Balance</h3>
                        <div className="d-flex align-items-center justify-content-between">
                            <h1 className="balance-amount">{moneyFormatter.format(hexaToDecimal(wallet.balance) / 10 ** 18)}</h1>
                        </div>
                        <p className="text-section">
                            Perfil Pblico: &nbsp;
                            <a onClick={() => history.push(`/${wallet.accounts[0]}`)} className="public-link" target="_blank" rel="noreferrer">
                                Ver Perfil
                            </a>
                        </p>
                    </div>
                    <div className="button-action-wrapper">
                        <Button
                            primary={'primary'}
                            icon="top-up"
                            onClick={() => {
                                return history.push(`/topup`);
                            }}
                        >
                            Ingresar
                        </Button>
                        <Button
                            primary={'primary'}
                            icon="transfer"
                            onClick={() => {
                                return history.push(`/search-receiver`);
                            }}
                        >
                            Transferir
                        </Button>
                        <Button
                            primary={'primary'}
                            icon="withdraw"
                            onClick={() => {
                                return history.push(`/topup`);
                            }}
                        >
                            Retirar
                        </Button>
                    </div>
                </div>
                <Cardwrapper>
                    <div className="activity-section card">
                        <div className="table-ads">
                            <p>Anuncios</p>
                            <div>
                                {ads?.length > 0 ? (
                                    ads.map?.((ad) => (
                                        <div className="offer-info" key={ad.adId}>
                                            <span>
                                                #{ad.adId} <b>{ad.isSeller ? 'Venta' : 'Compra'}</b>
                                            </span>
                                            <span>
                                                {`${formatAmount(ad.amount, parseInt(ad?.currencyData?.decimals) || 18)}`} {ad?.currencyData?.symbol || ad?.currency}
                                            </span>
                                            <span>
                                                <b>
                                                    ${ad.fiatPricePerToken} {ad.fiatCurrency}
                                                </b>
                                            </span>
                                            <span>{ad.paymentMethod}</span>
                                            <button>Modificar</button>
                                            <button onClick={() => handleAdAction('stopAd', ad.adId)}>Detener</button>
                                        </div>
                                    ))
                                ) : (
                                    <>
                                        <p>
                                            An no tienes anuncios.{' '}
                                            <a
                                                href="#"
                                                onClick={() => {
                                                    return history.push(`/account`);
                                                }}
                                            >
                                                Crear Anuncio
                                            </a>
                                        </p>
                                    </>
                                )}
                            </div>
                        </div>
                        <hr></hr>
                        <div className="table-orders">
                            <p>rdenes</p>
                            {orders?.length > 0 ? (
                                orders.map((order) => (
                                    <StyledOrder key={order.orderId}>
                                        <div>
                                            <span className="order-status">
                                                {OrderStatusIcon[order.status]} <b>{OrderStatusEnum[order.status]}</b>
                                            </span>
                                            <span className="last-update">{new Date(Number(order.updatedAt) * 1000).toLocaleString()}</span>
                                        </div>
                                        <div>
                                            <span>{order.value}</span>
                                            <span>{order.currency}</span>
                                        </div>
                                        <div>
                                            <span>{new Date(Number(order.createdAt) * 1000).toLocaleString()}</span>
                                        </div>
                                        <div>
                                            {compareAddresses(wallet?.accounts?.[0], order.receiver) && ['0', '2'].includes(order.status) ? (
                                                <Button onClick={() => handleOrderAction('fiatTransfered', order.orderId)}>Dinero Transferido</Button>
                                            ) : null}
                                            {(compareAddresses(wallet?.accounts?.[0], order.receiver) && ['0', '1', '2'].includes(order.status)) ||
                                            (compareAddresses(wallet?.accounts?.[0], order.sender) &&
                                                order.status === '0' &&
                                                (new Date() - new Date(Number(order.createdAt) * 1000)) / (1000 * 60) > 30) ? (
                                                <Button onClick={() => handleOrderAction('cancelOrder', order.orderId)}>Cancelar</Button>
                                            ) : null}
                                            {compareAddresses(wallet?.accounts?.[0], order.sender) && ['1', '2'].includes(order.status) ? (
                                                <Button onClick={() => handleOrderAction('releaseOrder', order.orderId)}>He recibido el dinero</Button>
                                            ) : null}

                                            {order.status === '1' && (new Date() - new Date(Number(order.createdAt) * 1000)) / 1000 > order.ad.expirationTime ? (
                                                <Button>Abrir Disputa</Button>
                                            ) : null}
                                        </div>
                                    </StyledOrder>
                                ))
                            ) : (
                                <span>
                                    No tienes rdenes activas.{' '}
                                    <a
                                        href="#"
                                        onClick={() => {
                                            return history.push(`/exchange`);
                                        }}
                                    >
                                        Ver Anuncios
                                    </a>
                                </span>
                            )}
                        </div>
                    </div>
                </Cardwrapper>
            </div>
            <div>
                <div className="transaction-history-wrapper">
                    <div className="main-wapper">
                        <div className="card mb-3">
                            <div className="heading">
                                <h2 className="text-heading">Monedero</h2>
                                <Link className="anchor" to={`/wallet`}>
                                    <p className="text-link">Ver Todo</p>
                                </Link>
                            </div>
                            <div className="body">
                                <div>
                                    {' '}
                                    <p>Blockchain Seleccionada</p>
                                    <p>
                                        Nombre: {ChainList?.[hexaToDecimal(wallet?.chainId)]?.name || 'No se encontr nombre'} {`${hexaToDecimal(wallet?.chainId)}`}
                                    </p>
                                </div>
                                <div>
                                    <p>
                                        Contrato CambioVirtual:{' '}
                                        <a rel="noreferrer" target="_blank" href={`${ChainList?.[hexaToDecimal(wallet?.chainId)]?.explorerAddress}${deployedAddress}`}>
                                            {deployedAddress}
                                        </a>{' '}
                                    </p>
                                </div>
                                <hr></hr>
                                <div>
                                    {wallet.accounts.length > 0 && (
                                        <>
                                            <div>
                                                Direccin de tu Billetera:{' '}
                                                <a rel="noreferrer" target="_blank" href={`${ChainList?.[hexaToDecimal(wallet?.chainId)]?.explorerAddress}${wallet.accounts[0]}`}>
                                                    {wallet.accounts[0]}
                                                </a>{' '}
                                            </div>

                                            <div>
                                                GAS disponible: {hexaToDecimal(wallet.balance) / 10 ** 18} {ChainList?.[hexaToDecimal(wallet?.chainId)]?.symbol}
                                            </div>
                                        </>
                                    )}
                                </div>
                                <hr></hr>
                                <div>
                                    <p>Balances en el Contrato</p>
                                    {balances?.length > 0 ? (
                                        balances.map((balance, i) => (
                                            <div key={i}>
                                                Balance: <span>{((balance?.amount || 0) / 10 ** (balance?.currencyData?.decimals || 18)).toFixed(4)}</span>{' '}
                                                <span>{balance?.currencyData?.symbol || ''}</span>
                                                <p>
                                                    Contrato <span>{balance?.currencyData?.symbol || ''}</span>:{' '}
                                                    <a
                                                        rel="noreferrer"
                                                        target="_blank"
                                                        href={`${ChainList?.[hexaToDecimal(wallet?.chainId)]?.explorerAddress}${balance?.currency}`}
                                                    >
                                                        {balance?.currency}
                                                    </a>
                                                </p>
                                                <div>
                                                    <Button onClick={() => handleClaim(balance?.currency)}>Claim</Button>
                                                </div>
                                            </div>
                                        ))
                                    ) : (
                                        <p>No tienes balances en el contrato</p>
                                    )}
                                </div>
                                <hr></hr>
                                <div>
                                    <p>Acciones de CambioVirtual</p>
                                    <p>
                                        Contrato:{' '}
                                        <a rel="noreferrer" target="_blank" href={`${ChainList?.[hexaToDecimal(wallet?.chainId)]?.explorerAddress}${shares?.token}`}>
                                            {shares?.token}
                                        </a>
                                    </p>
                                    <p>Nombre: {shares?.name}</p>
                                    <p>
                                        Tus acciones: {shares?.balanceOf} {shares?.symbol}  %{shares?.balanceOf / 1000000}
                                    </p>
                                    <p>Cantidad Total: 100.000.000</p>
                                </div>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </StyledDashboard>
    );
};

Homepage.propTypes = {};

export default Homepage;

const StyledOrder = styled.div`
    width: 100%;
    display: flex;
    align-items: center;
    justify-content: space-between;
    border-radius: 8px;
    padding: 10px 15px;
    overflow: auto;
    div {
        display: flex;
        align-items: center;
        justify-content: center;
        flex-direction: column;
    }

    .order-status {
        display: flex;
        align-items: center;
        font-size: 15px;
    }

    .order-status b {
        font-size: 18px;
        margin-left: 8px;
    }

    .last-update {
        color: #bdbdbd;
        font-size: 14px;
    }

    .table-headers {
        width: 100%;
        justify-content: space-between;
    }
`;
