import { toast } from 'react-toastify';

export const toastify = (message, type) => {
  const types = {
    'success': 'success',
    'warning': 'warn',
    'error': 'error'
  }
  const call = types?.[type] ? types?.[type] : 'info';
  return toast[call]( message, {
    position: 'top-right',
    autoClose: 5000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
  });
  
};
