import React, { useEffect } from 'react';
import styled from 'styled-components';
import { Cardwrapper } from '../../components';

const iframe = `<div id="swapzoneExchangeWidget" 
                  data-theme="light" 
                  data-size="full" 
                  data-refid="cXHnxAsFvM"
                  data-from="usdtmatic" 
                  data-to="btc" 
                ></div> `;

const createMarkup = () => {
  return {__html: iframe};
}

const ToolsPage = () => {

  
  useEffect(() => {
    const script = document.createElement('script');
  
    script.src = "https://swapzone.io/script/exchange-widget.js";

    document.body.appendChild(script);
    
    return () => {
      document.body.removeChild(script);
    }
  }, []);
  return (
    <>
      <Cardwrapper>
        <StyledTopupPage>
        {/* <HeadingContent>Exchange</HeadingContent> */}
        <div className="body-content">
          <div dangerouslySetInnerHTML={createMarkup()} />
        </div> 
        </StyledTopupPage> 
      </Cardwrapper>
      </>
  );
};

export default ToolsPage;

const StyledTopupPage = styled.div`
  padding: 50px 10px;
  .widget {
    max-width: 100%;
  }
  .body-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .item {
      padding: 30px 20px;
      display: flex;
      gap: 1rem;
      p {
        margin-bottom: 0;
      }
      .number {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #6379f4;
      }
    }
  }
`;
