import crypto from 'crypto';

const algorithm = 'aes-256-cbc';
const iv = Buffer.alloc(16, 0);

export const cipherData = (key, text) => {
  try {
      const bufferKey = Buffer.alloc(32, key, 'utf8');
      const cipher = crypto.createCipheriv(algorithm, bufferKey, iv);
      let encrypted = cipher.update(text, 'utf8', 'hex');
      encrypted += cipher.final('hex');
      return encrypted;
  } catch (error) {
      console.log(error);
      return false;
  }
}

export const decipherData = (key, text) => {
  try {
      const bufferKey = Buffer.alloc(32, key, 'utf8');
      const decipher = crypto.createDecipheriv(algorithm, bufferKey, iv);
      let decryptedText = decipher.update(text, 'hex', 'utf8');
      decryptedText += decipher.final('utf8');
      return decryptedText;
  } catch (error) {
      console.log(error);
      return false;
  }
}