import { makeStyles } from '@material-ui/core/styles';
import FilterListIcon from '@material-ui/icons/FilterList';
import Pagination from '@material-ui/lab/Pagination';
import { useDispatch } from 'react-redux';
import axios from 'axios';
import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { AVAUserDefault } from '../../assets';
import { useHistory } from 'react-router-dom';
import { CardProfileUser, Cardwrapper, HeadingContent } from '../../components';
import { isAddress, objectToUriParams } from '../../utils'
import { IoSearchSharp } from "react-icons/io5";
import { showLoading } from '../../config/Redux/actions';

const SearchReceiverPage = () => {
    const [searchAllResult, setSearchAllResult] = useState([]);
    const [paginationState, setPaginationState] = useState();
    const [currentPage, setCurrentPage] = useState();
    const [searchKeyword, setSerchKeyword] = useState('');
    const token = localStorage.getItem('token');
    const history = useHistory();
    const dispatch = useDispatch();

    useEffect(() => {
        window.scrollTo(0, 0);
    }, [])

    const actionSearch = async () => {
        await getSearchResults({search: searchKeyword, page: 1});
    };

    
    useEffect(() => {
        if ((!searchKeyword?.length || searchKeyword?.length > 3) && !searchKeyword.startsWith('0x'))  {
            actionSearch(); 
        }
           
        if (isAddress(searchKeyword)) {
            dispatch(showLoading(true));
            history.push(`search-receiver/${searchKeyword}`); 
            dispatch(showLoading(false))
        }
        // eslint-disable-next-line
    }, [searchKeyword, dispatch, history]);

    
    useEffect(() => {
        getSearchResults();
        // eslint-disable-next-line
    }, []);

    const useStyles = makeStyles((theme) => ({
        root: {
            '& > *': {
                marginTop: theme.spacing(2),
            },
        },
    }));

    function PaginationOutlined() {
        const classes = useStyles();
        const handlePagination = async (event, value) => {
            setCurrentPage(value);
            await getSearchResults({page: value});
        };

        return (
            <div className={classes.root}>
                <Pagination
                    count={paginationState?.totalPage}
                    page={currentPage}
                    variant="outlined"
                    onChange={handlePagination}
                />
            </div>
        );
    }

    const [sorting, setSorting] = useState('ASC');

    const actionSort = async () => {
        sorting === 'ASC' ? setSorting('DESC') : setSorting('ASC');
        await getSearchResults({order: sorting});
    };

    const getSearchResults = async (params = {}) => {
        const queryParams = { ...params, perPage: 5 };
        const result = await axios
            .get(
                `${process.env.REACT_APP_BACKEND_API}/users?${objectToUriParams(queryParams)}`,
                { headers: { Authorization: `Bearer ${token}` } }
            ).catch((err) => { console.log(err) });
        const { data, meta } = result?.data || {};
        if (data) { setSearchAllResult(data) }
        if (meta) { setPaginationState(meta) }
                
    }

    return (
        <>
            <Cardwrapper>
                <StyledSearchPage sorting={sorting}>
                    <HeadingContent>Transferir Dinero</HeadingContent>
                    <div className="search-section">
                        < IoSearchSharp style={{ fontSize: '1.5rem', color: '#C8C8C9' }} />
                        <div>
                            <input
                                className="input-search"
                                placeholder="Direccin de Destino"
                                type="text"
                                value={searchKeyword}
                                name="search"
                                onChange={(e) => setSerchKeyword(e.target.value)}
                            />
                        </div>
                    </div>
                    <div className="sort-btn" onClick={actionSort}>
                        <FilterListIcon className="btn-sort" />
                    </div>
                    <div className="body-section">
                        {searchAllResult?.length > 0 &&
                            searchAllResult.map((item, index) => {
                                return (
                                    <CardProfileUser
                                        key={index}
                                        avatar={item.avatar ? item.avatar : AVAUserDefault}
                                        link={`search-receiver/${item.username}`}
                                        username={item.username}
                                    />
                                );
                            })}
                    </div>
                </StyledSearchPage>
            </Cardwrapper>
            <PaginationOutlined />
        </>
    );
};

export default SearchReceiverPage;

const StyledSearchPage = styled.div`
  padding: 30px;
  .search-section {
    margin-bottom: 50px;
    background: rgba(58, 61, 66, 0.1);
    border-radius: 12px;
    box-sizing: content-box;
    padding: 15px;
    display: flex;
    gap: 1rem;
    &:hover {
      cursor: pointer;
      opacity: 0.7;
    }
    .icon {
    }
    div {
      width: 100%;
      .input-search {
        width: 100%;
        height: 23px;
        border: 0;
        background-color: transparent;
        &:focus {
          outline: none;
        }
      }
      .input-search::placeholder {
        color: #aaa;
      }
    }
  }
  .body-section {
    display: flex;
    flex-direction: column;
    gap: 2rem;
    /* background-color: green; */
  }
  .sort-btn {
    text-align: right;
    .btn-sort {
      transform: ${({ sorting }) =>
        sorting === 'ASC' ? 'rotate(0deg)' : 'rotate(180deg)'};
      &:hover {
        cursor: pointer;
        opacity: 0.5;
      }
    }
  }
`;
