import PropTypes from 'prop-types';
import React from 'react';
import { useHistory } from 'react-router-dom';
import styled from 'styled-components';
import { AVAUserDefault } from '../../../assets';
import { FaTelegram } from 'react-icons/fa';
import { shortAddress } from '../../../utils';

const transactionTypes = {
  transfer: { text: 'Transferencia Enviada', color: 'inherit' },
  topup: { text: 'Transferencia Recibida', color: 'green' }
}

const CardProfileUser = ({
  avatar,
  username,
  wallet,
  createdAt,
  typeTransaction,
  amount,
  link,
}) => {
  const history = useHistory();

  const actionRedirect = (link) => {
    history.push(link);
  };

  const isTopup = !!(typeTransaction?.toLowerCase() === 'topup');

  const HexToColorVector = ({ hexString }) => {
    // Transforma una cadena hexadecimal en un array de colores
    const hexToColors = (hex) => {
      let colors = [];
  
      for (let i = 0; i < hex.length; i += 6) {
        const colorSegment = hex.substring(i, i + 6);
        colors.push('#' + colorSegment);
      }
  
      return colors;
    }
  
    const colors = hexToColors(hexString);

    console.log(colors)
  
    return (
      <div style={{ display: 'flex' }}>
        {colors.slice(1).map((color, index) => (
          <div
            key={index}
            style={{
              backgroundColor: color,
              width: '15px',
              height: '15px',
              borderRadius: '3px',
              marginRight: '1px'
            }}
          ></div>
        ))}
      </div>
    );
  }

  return (
    <StyledHistoryItem
      className="history-item"
      onClick={() => actionRedirect(link)}
      link={link}
    >
      <div className="left">
        <div className="avatar-wrapper" style={{border: `1.5px solid #${wallet.substring(2, 8)}cc`}}>
          <span style={{color: `#${wallet.substring(2, 8)}`}}>{username.slice(0,2).toUpperCase()}</span>
        </div>
        <div className="user-name">
          {
            transactionTypes[typeTransaction?.toLowerCase()]?.text ? (
              <>
                <h4 className="text-heading">{transactionTypes[typeTransaction?.toLowerCase()]?.text}</h4>
                <p className="text">{ isTopup ? 'de' : 'a' } {username}</p>
                { wallet && <HexToColorVector hexString={wallet} /> }
              </>
              
            ) : (
              <>
                <h4 className="text-heading"><a target='_blank' href={`https://t.me/${username}`} className='telegram-wrap' rel="noreferrer">
                  {username} 
                  <FaTelegram className="telegram-icon" />
                </a> </h4>
                { wallet && <p className="text">{shortAddress(wallet)}</p> }
              </>
            )
          }
          
        </div>
      </div>
      {amount && (
        <div className="amount-wrapper">
          <h4 className="text-heading" style={{color: transactionTypes[typeTransaction?.toLowerCase()]?.color}}> 
            {typeTransaction?.toLowerCase() === 'topup' ? 
            '+' : '-' }{amount}</h4>
            { createdAt && (<p className="text">{new Date(createdAt).toLocaleDateString()}</p>)}
        </div>
      )}
    </StyledHistoryItem>
  );
};

CardProfileUser.propTypes = {
  avatar: PropTypes.string.isRequired,
  username: PropTypes.string.isRequired,
  typeTransaction: PropTypes.string.isRequired,
  // amount: PropTypes.string.isRequired,
  link: PropTypes.string,
};

CardProfileUser.defaultProps = {
  avatar: AVAUserDefault,
  username: '...',
  typeTransaction: '...',
};

export default CardProfileUser;

const StyledHistoryItem = styled.div`
.telegram-icon {
    width: 15px;
    height: auto;
    margin-left: 5px;
  }
  .telegram-wrap {
    text-decoration: none;
    display: flex;
    align-items: center;
    justify-content: center;
  }
  &:hover {
    opacity: ${({ link }) => link && 0.5};
    cursor: ${({ link }) => link && 'pointer'};
  }
  display: flex;
  align-items: center;
  justify-content: space-between;
  .left {
    display: flex;
    width: 100%;
  }
  .avatar-wrapper {
    width: 50px;
    height: 50px;
    margin-right: 1rem;
    align-items: center;
    border-radius: 100%;
    display: flex;
    flex-shrink: 0;
    font-size: 14px;
    font-weight: 600;
    justify-content: center;
    margin-top: 2px;
    @media screen and (max-width: 1500px) { 
      width: 35px;
      height: 35px;
      margin-right: 8px;
      font-size: 12px;
    }
    img {
      object-fit: cover;
      border-radius: 50%;
      width: 100%;
      height: 100%;
    }
  }
  .user-name {
    display: flex;
    align-items: flex-start;
    flex-direction: column;
    justify-content: center;
    .text-heading, .text-heading a {
      margin: 0;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 22px;
      color: rgba(58,61,66,0.8);
      white-space: nowrap;
      @media screen and (max-width: 1500px) { 
        font-size: 12px;
      }
    }

    .text-heading a:hover {
      color: #6379F4;
    }
    p {
      margin: 0;
      font-size: 12px;
      @media screen and (max-width: 1500px) { 
        font-size: 10px;
      }
    }
  }
  .amount-wrapper {
    display: flex;
    align-items: flex-end;
    flex-direction: column;
    justify-content: center;
    margin-left: 5px;
    .text-heading {
      margin: 0;
      font-style: normal;
      font-weight: bold;
      font-size: 14px;
      line-height: 22px;
      white-space: nowrap;
    }
    p {
      margin: 0;
      font-size: 12px;
      text-align: right;
      white-space: nowrap;
    }
  }
`;
