import axios from 'axios';
import { toastify } from '../../../utils';

export const requestWallet = async (data) => {
  const token = localStorage.getItem("token");
  console.log(data)
  axios
    .post(`${process.env.REACT_APP_BACKEND_API}/wallets/create`, 
      data,  
      { headers: {  'Access-Control-Allow-Origin': '*', 'Authorization': `Bearer ${token}`, }})
    .then((result) => {
      console.log(result);
      toastify(
        'Monedero creado con xito.',
        'info'
      );
    })
    .catch((error) => {
      console.log(error);
      return toastify(error.response.data.message, 'error');
    });
}