import React, { useState, useEffect } from 'react';
import styled from 'styled-components';
import { Cardwrapper } from '../../components';
import Tabs from '../../components/atoms/Tabs';
import { useDispatch } from 'react-redux';
import { customMedia } from '../../components/Layouting/BreakPoints';
import Web3 from 'web3';
import { ERC20ABI, shortAddress, call } from '../../utils';
import detectEthereumProvider from '@metamask/detect-provider';
import { ContractsList } from '../../config/config';
import { colors } from '../../utils';
import { dispatchTypes } from '../../utils/dispatchType';
import Tooltip from 'react-bootstrap/Tooltip';
import OverlayTrigger from 'react-bootstrap/OverlayTrigger';
import { useHistory } from 'react-router-dom/cjs/react-router-dom';
import { zeroAddress } from 'ethereumjs-util';

const P2PPage = () => {
    const dispatch = useDispatch();
    const history = useHistory();
    const [activeAds, setActiveAds] = useState([]);
    const initialState = { accounts: [], balance: '', chainId: '' };
    const [wallet, setWallet] = useState(initialState);

    const getCurrency = async (address) => {
        const isOnList = ContractsList?.[wallet?.chainId]?.[address];
        if (isOnList) {
            return isOnList;
        }
        const web3Instance = new Web3(window.ethereum);
        const tokenContract = new web3Instance.eth.Contract(ERC20ABI, address);
        const name = await tokenContract.methods?.name?.()?.call();
        const symbol = await tokenContract.methods?.symbol?.()?.call();
        const decimals = await tokenContract.methods?.decimals?.()?.call();
        const balanceOf = await tokenContract.methods?.balanceOf?.(wallet.accounts[0])?.call();

        return { name, symbol, decimals, balanceOf };
    };

    const handleConnect = async () => {
        let accounts = await window.ethereum.request({
            method: 'eth_requestAccounts'
        });
        updateWallet(accounts);
    };

    useEffect(() => {
        const refreshAccounts = (accounts) => {
            if (accounts?.length > 0) {
                updateWallet(accounts);
            } else {
                setWallet(initialState);
            }
        };

        const refreshChain = async (chainId) => {
            setWallet((wallet) => ({ ...wallet, chainId }));
            const accounts = await window.ethereum.request({ method: 'eth_accounts' });
            refreshAccounts(accounts);
        };

        const getProvider = async () => {
            const provider = await detectEthereumProvider({ silent: true });

            if (provider) {
                const accounts = await window.ethereum.request({ method: 'eth_accounts' });

                if (accounts[0] !== wallet.accounts[0]) refreshAccounts(accounts);
                window.ethereum.on('accountsChanged', refreshAccounts);
                window.ethereum.on('chainChanged', refreshChain);
            }
        };
        getProvider();

        return () => {
            window.ethereum?.removeListener('accountsChanged', refreshAccounts);
            window.ethereum?.removeListener('chainChanged', refreshChain);
        };
        // eslint-disable-next-line
    }, [wallet]);

    const updateWallet = async (accounts) => {
        const balance = await window.ethereum.request({
            method: 'eth_getBalance',
            params: [accounts[0], 'latest']
        });
        const chainId = await window.ethereum.request({
            method: 'eth_chainId'
        });
        const newWallet = { accounts, balance: hexaToDecimal(balance), chainId: hexaToDecimal(chainId), hexaChainId: chainId };
        setWallet(newWallet);
        dispatch({ type: dispatchTypes.setWalletAccount, payload: newWallet });
    };

    useEffect(() => {
        const loadActiveAds = async () => {
            try {
                const ads = await call('getActiveAds');
                let adsInfo = [];
                for (let item of ads) {
                    const currencyData = await getCurrency(item.currency);
                    adsInfo.push({ ...item, currencyData });
                }
                setActiveAds(adsInfo);
            } catch (error) {
                console.error('Error while loading active ads:', error);
            }
        };

        loadActiveAds();
        // eslint-disable-next-line
    }, [wallet]);

    useEffect(() => {
        handleConnect();
        // eslint-disable-next-line
    }, []);

    const hexaToDecimal = (hexString) => {
        if (!hexString) return '';
        return parseInt(hexString.replace(/^0x/, ''), 16);
    };

    const tokenList = activeAds
        .map((ad) => ({
            currency: ad.currency,
            currencyData: ad.currencyData
        }))
        .sort((a, b) => (a.currency > b.currency ? 1 : -1));

    const reducedList = Object.values(tokenList.reduce((acc, obj) => ({ ...acc, [obj.currency]: obj }), {})).filter( token => token?.currency === zeroAddress() ? wallet?.balance > 0 : token?.currencyData?.balanceOf > 0);

    return (
        <>
            <StyledHeader>
                <div className="header-section">
                    <div className="balance-section">
                        {reducedList.length > 0 ?
                            reducedList.map((token) => (
                                <div className="balance-wrapper" key={token?.currency}>
                                    <span className="text-section">{token.currencyData.symbol}</span>
                                    <div className="d-flex align-items-center justify-content-between">
                                        <h1 className="balance-amount">
                                            {' '}
                                            {(
                                                ((token?.currency === zeroAddress() ? wallet?.balance : token?.currencyData?.balanceOf) || 0) /
                                                10 ** (token?.currencyData?.decimals || 18)
                                            ).toFixed(4)}{' '}
                                        </h1>
                                    </div>
                                </div>
                            )) : <div></div>}
                    </div>
                    {wallet?.balance <= 0 && <div className='balance-alert'>Tu saldo no es suficiente para cubrir los costos de gas. Recrgalo aqu.</div>}
                </div>
            </StyledHeader>
            <Cardwrapper>
                <StyledP2PPage>
                    <div className="body-content">
                        <div className="buysell-tabs">
                            <Tabs activeAds={activeAds} wallet={wallet} />
                        </div>
                    </div>
                </StyledP2PPage>
            </Cardwrapper>
        </>
    );
};

export default P2PPage;

const StyledHeader = styled.div`
    margin-bottom: 1rem;
    .public-link {
        text-decoration: none;
        color: white;
        font-weight: 600;
        max-width: 100%;
        overflow: hidden;
    }

    .header-section {
        background-color: ${colors.primary};
        width: 100%;
        display: flex;
        align-items: center;
        ${customMedia.lessThan('1000px')` 
            flex-direction: column;
        `}

        justify-content: space-between;
        padding: 30px;
        border-radius: 20px;
        ${customMedia.lessThan('1000px')` 
            border-radius: 8px;
        `}
        .balance-section {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            flex-wrap: wrap;
            gap: 1rem;
        }
        .balance-wrapper {
            .text-section {
                font-family: Nunito Sans;
                font-style: normal;
                font-weight: normal;
                font-size: 18px;
                line-height: 31px;
                color: #e0e0e0;
                margin: 0;
                .add-phone-number {
                    color: white;
                }
            }
            .balance-amount {
                font-family: Nunito Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                margin: 0;
                line-height: 31px;
                color: #ffffff;
            }
            div {
                gap: 1rem;
            }
        }
        .balance-alert {
            color: white;
            font-size: 15px;
            line-height: 1.6;
            text-align: center;
        }
        .button-action-wrapper {
            display: flex;
            ${customMedia.lessThan('1000px')` 
          margin-top: 1rem;
      `}
            gap: 1rem;
            flex-direction: column;
            .button-action {
                background: rgba(255, 255, 255, 0.2);
                border: 1px solid #ffffff;
                box-sizing: border-box;
                border-radius: 10px;
                padding: 14px 27px;
                color: white;
                font: inherit;
                svg {
                    margin-right: 1rem;
                }
                &:hover {
                    opacity: 0.8;
                }
            }
        }
    }
`;

const StyledP2PPage = styled.div`
    padding: 30px;
    ${customMedia.lessThan('625px')`
            padding: 18px;
        `}
    nav {
        display: flex;
        align-items: center;
        justify-content: space-between;
        ${customMedia.lessThan('625px')`
            flex-direction: column-reverse;
        `}
    }
    nav span {
        ${customMedia.lessThan('625px')`
            align-self: flex-start;
        `}
    }
    .btn-create {
        font-size: 16px;
        font-weight: 600;
        max-width: 180px;
        padding: 8px 10px;
        background-color: white;
        color: #6379f4;
        ${customMedia.lessThan('625px')`
            align-self: flex-end;
            padding: 0;
            margin-bottom: 1rem;
        `}
    }
    .body-content {
        display: flex;
        flex-direction: column;
        gap: 1rem;

        button {
            cursor: pointer;
        }
        .item {
            padding: 30px 20px;
            display: flex;
            gap: 1rem;
            p {
                margin-bottom: 0;
            }
            .number {
                font-family: Nunito Sans;
                font-style: normal;
                font-weight: bold;
                font-size: 18px;
                line-height: 25px;
                color: #6379f4;
            }
        }
    }
`;
