import styled from 'styled-components';
import { customMedia } from '../../components/Layouting/BreakPoints';
import { colors } from '../../utils';
export const StyledDashboard = styled.main`
  width: 100%;
  height: 100%;
  display: flex;
  ${customMedia.lessThan('1000px')` 
  max-width: 100%;
    `}
  /* STYLING FOR HEADER IN HOMEPAGE */
  .middle {
    min-width: 60%;
    margin-right: 1rem;
  }
  .public-link {
    text-decoration: none;
    color: white;
    font-weight: 600;
  }
  
  .header-section {
    background-color: ${colors.primary};
    width: 100%;
    display: flex;
    ${customMedia.lessThan('1000px')` 
    flex-direction: column;
    `}
    
    justify-content: space-between;
    padding: 30px;
    border-radius: 20px;

    .balance-wrapper {
      .text-section {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: normal;
        font-size: 18px;
        line-height: 31px;
        color: #e0e0e0;
        margin: 0;
        .add-phone-number {
          color: white;
        }
      }
      .balance-amount {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 40px;
        line-height: 55px;
        color: #ffffff;
      }
      div {
        gap: 1rem;
      }
    }
    .button-action-wrapper {
      display: flex;
      ${customMedia.lessThan('1000px')` 
          margin-top: 1rem;
      `}
      gap: 1rem;
      flex-direction: column;
      .button-action {
        background: rgba(255, 255, 255, 0.2);
        border: 1px solid #ffffff;
        box-sizing: border-box;
        border-radius: 10px;
        padding: 14px 27px;
        color: white;
        font: inherit;
        svg {
          margin-right: 1rem;
        }
        &:hover {
          opacity: 0.8;
        }
      }
    }
  }

  .card {
      background: #ffffff;
      box-shadow: 0px 4px 20px rgba(0, 0, 0, 0.10);
      border-radius: 25px;
      padding: 30px;
      border: none;
    }

  .activity-section {
    margin-top: 1rem;
  }
  /* STYLING FOR TRANSACTION, CHART, IN HOMEPAGE */
  .transaction-history-wrapper {
    display: flex;
    ${customMedia.lessThan('1162px')`
      flex-direction: column; 
    `}
    gap: 1rem;
    
    /* START = GLOBAL STYLING ONLY THIS SCOPE */
    

    /* END = GLOBAL STYLING ONLY THIS SCOPE */
    .main-wapper {
      width: 100%;
      ${customMedia.lessThan('1162px')` 
        width: 100%;
      `}
      .heading {
        display: flex;
        ${customMedia.lessThan('1265px')`
          flex-direction: column; 
          `}
        ${customMedia.lessThan('1162px')` 
           flex-direction: row;
      `}
        justify-content: space-between;
        margin-bottom: 1rem;
        h2 {
          margin: 0;
        }
        p {
          margin: 0;
          line-height: 24px;
        }
      }
      .body-chart {
        margin-top: 80px;
      }
    }
    .body p {
        margin-bottom: 0;
      }
    .history-wrapper {
      width: 40%;
      margin-bottom: 1rem;
      ${customMedia.lessThan('1162px')` 
        width: 100%;
      `}
      .heading {
        display: flex;
        ${customMedia.lessThan('1265px')`
          flex-direction: column; 
          `}
        ${customMedia.lessThan('1162px')` 
           flex-direction: row;
      `}
        justify-content: space-between;
        margin-bottom: 1rem;
        h2 {
          margin: 0;
        }
        p {
          margin: 0;
          line-height: 24px;
        }
      }
      .body {
        display: flex;
        flex-direction: column;
        gap: 30px;
      }
      
      .body-chart {
        width: 100px;
        height: 100px;
        background: black;
      }
    }
  }
`;
