import Web3 from 'web3';
import { CambioVirtualJSON, ERC20ABI, hexaToDecimal } from './';
import { isAddress } from './common';
import { zeroAddress } from 'ethereumjs-util';

let contract, wallet, networkId;

async function init() {
    const web3Instance = new Web3(window.ethereum);
    wallet = await window.ethereum.request({ method: 'eth_accounts' });
    const networkIdHexa = await window.ethereum.request({ method: 'eth_chainId' });
    networkId = hexaToDecimal(networkIdHexa);
    const deployedNetwork = CambioVirtualJSON.networks[networkId];
    if (!deployedNetwork?.address) {
        throw new Error('Contract not deployed.');
    }
    contract = new web3Instance.eth.Contract(CambioVirtualJSON.abi, deployedNetwork.address);
}

async function allowance(currency, amount) {
    const web3Instance = new Web3(window.ethereum);
    const tokenContract = new web3Instance.eth.Contract(ERC20ABI, currency);
    const deployedNetwork = CambioVirtualJSON.networks[networkId];
    const tokenAllowance = await tokenContract.methods?.allowance?.(wallet[0], deployedNetwork.address)?.call();
    if (tokenAllowance < parseFloat(amount)) {
        const diff = (amount - tokenAllowance).toString();
        await tokenContract.methods?.approve?.(deployedNetwork.address, diff)?.send({ from: wallet[0] });
    }
}

/***
    ads(uint256 _adId);
    balances(address _addr, IERC20 _currency);
    disputeDuration();
    disputes(uint256 _orderId);
    fee();
    getActiveAds();
    getActiveOffers();
    getAdsByAddress(address _addr);
    getDisputes();
    getMyShares();
    getOrdersByAd(uint256 _adId);
    getOrdersByAddress(address _addr);
    nextAdId();
    nextOrderId();
    offers(address _addr, IERC20 _currency);
    offersList(uint256 _index);
    orders(uint256 _orderId);
    owners(address _addr);
    ownersList(uint256 _index);
    points(address _addr);
    totalShares();
    votes(uint256 _orderId, address _addr);
    votingStartTime();
***/
export const call = async (action, params = []) => {
    await init();
    if (typeof contract?.methods?.[action] !== 'function' || !wallet?.[0]) {
        return null;
    }
    const result = await contract.methods[action](...params).call({
        from: wallet[0]
    });
    return result;
};

/*** 
    cancelOrder(uint256 orderId);
    claim(address _currency);
    disputeOrder(uint256 _orderId);
    fiatTransfered(uint256 _orderId);
    releaseOrder(uint256 _orderId);
    resolveDispute(uint256 _orderId);
    stopAd(uint256 _adId);
    transferBalance(address _receiver, address _currency, uint256 _amount);
    vote(uint256 _orderId, bool _release);
***/
export const send = async (action, params = []) => {
    await init();
    if (typeof contract?.methods?.[action] !== 'function' || !wallet?.[0]) {
        return null;
    }
    const result = await contract.methods[action](...params).send({
        from: wallet[0]
    });
    return result;
};

/*** 
    //Payable Functions 
    createAd(
        string _user, 
        bool _isSeller, 
        uint256 _amount, 
        address _currency,
        uint256 _fiatPricePerToken,
        string _fiatCurrency,
        string _paymentMethod,
        uint256 _fiatMinValue,
        uint256 _expirationTime
    );
    createOrder(uint256 _adId, uint256 _value, string _user);
    deposit(address _currency, uint256 _amount);
    modifyAd(uint256 _adId, uint256 _fiatPricePerToken, uint256 _amountToAdd, uint256 _fiatMinValue);
***/

export const deposit = async (currency, amount) => {
    await init();
    if (!isAddress(currency) || amount <= 0 || !wallet?.[0]) {
        throw new Error('Invalid Params');
    }
    const isNative = currency === zeroAddress();
    if (!isNative) {
        await allowance(currency, amount); 
    }
    await contract.methods.deposit(currency, amount).send({
        from: wallet[0],
        ...(isNative ? { value: amount } : {})
    });
};

export const createOrder = async (adId, value, user) => {
    await init();
    const ad = await call('ads', [adId]);
    if (value <= 0 || !wallet?.[0] || !ad) {
        throw new Error('Invalid Params');
    }
    const isNative = ad.currency === zeroAddress();
    if (!ad.isSeller && !isNative) {
        await allowance(ad.currency, value);
    }
    await contract.methods.createOrder(adId, value, user).send({
        from: wallet[0],
        ...(!ad.isSeller && isNative ? { value } : {})
    });
};

export const createAd = async (user, isSeller, amount, currency, fiatPricePerToken, fiatCurrency, paymentMethod, fiatMinValue, expirationTime) => {
    await init();
    if (amount <= 0 || !wallet?.[0] || !isAddress(currency)) {
        throw new Error('Invalid Params');
    }
    const isNative = currency === zeroAddress();
    if (isSeller && !isNative) {
        await allowance(currency, amount);
    }
    await contract.methods.createAd(user, isSeller, amount, currency, fiatPricePerToken, fiatCurrency, paymentMethod, fiatMinValue, expirationTime).send({
        from: wallet[0],
        ...(isSeller && isNative ? { value: amount } : {})
    });
};

export const modifyAd = async (adId, fiatPricePerToken = 0, amountToAdd = 0, fiatMinValue = 0) => {
    await init();
    const ad = await call('ads', [adId]);
    if (amountToAdd <= 0 || !wallet?.[0] || !ad) {
        throw new Error('Invalid Params');
    }
    const isNative = ad.currency === zeroAddress();
    if (ad.isSeller && !isNative) {
        await allowance(ad.currency, amountToAdd);
    }
    await contract.methods.modifyAd(adId, fiatPricePerToken, amountToAdd, fiatMinValue).send({
        from: wallet[0],
        ...(ad.isSeller && isNative ? { value: amountToAdd } : {})
    });
};

