import React, { useState, useEffect } from 'react';
import Web3 from 'web3';
import styled from 'styled-components';
import PropTypes from 'prop-types';
import CardProfileUser from '../CardProfileUser';
import Button from '../Button';
import Filters from '../Filters';
import { ContractsList } from '../../../config/config';
import Form from 'react-bootstrap/Form';
import Container from 'react-bootstrap/Container';
import Row from 'react-bootstrap/Row';
import Col from 'react-bootstrap/Col';
import { CambioVirtualJSON, ERC20ABI, call, OrderStatusEnum, createOrder } from '../../../utils';
import { useDispatch } from 'react-redux';
import { showLoading } from '../../../config/Redux/actions';
import { IoAlarmOutline } from 'react-icons/io5';

const fiatDecimals = 10 ** 4;

const Index = ({ activeAds, wallet }) => {
    const dispatch = useDispatch();
    const [activeTab, setActiveTab] = useState(0);
    const [contract, setContract] = useState(null);
    const [order, setOrder] = useState({
        adId: '',
        cryptoValue: '',
        fiatValue: '',
        user: ''
    });
    const [adOrders, setAdOrders] = useState([]);

    const tabs = [
        {
            label: 'Comprar',
            content: activeAds.filter((x) => x.isSeller).sort((a, b) => (a.fiatPricePerToken > b.fiatPricePerToken ? 1 : -1))
        },
        {
            label: 'Vender',
            content: activeAds.filter((x) => !x.isSeller).sort((a, b) => (a.fiatPricePerToken > b.fiatPricePerToken ? -1 : 1))
        }
    ];

    const info = tabs[activeTab].content;
    const handleTabClick = (index) => {
        setActiveTab(index);
    };

    const formatAmount = (amount, decimals) => {
        let resultado = parseFloat(parseFloat(amount) / 10 ** decimals).toString();
        return resultado.startsWith('0.') ? resultado.replace(/\.?0+$/, '') : parseFloat(resultado);
    };

    const handleCreateOrder = async (ad) => {
        try {
            const decimals = ad?.currencyData?.decimals;
            if (!decimals) {
                return;
            }
            dispatch(showLoading(true));
            const amountToWei = (parseFloat(order.cryptoValue) * 10 ** parseInt(decimals)).toString();
            const result = await createOrder(order.adId, amountToWei, order.user);
            console.log(result);
            loadActiveAds();
        } catch (error) {
            console.log(error);
            dispatch(showLoading(false));
        }
    };

    useEffect(() => {
        const web3Instance = new Web3(window.ethereum);
        const networkId = wallet.chainId;
        const deployedNetwork = CambioVirtualJSON.networks[networkId];
        if (deployedNetwork) {
            const escrowContract = new web3Instance.eth.Contract(CambioVirtualJSON.abi, deployedNetwork && deployedNetwork.address);
            setContract(escrowContract);
        }
    }, [wallet]);

    const loadActiveAds = async () => {
        if (contract) {
            try {
                dispatch(showLoading(true));
                const ads = await contract.methods.getActiveAds().call();
                let adsInfo = [];
                for (let item of ads) {
                    const currencyData = await getCurrency(item.currency);
                    adsInfo.push({ ...item, currencyData });
                }
                dispatch(showLoading(false));
                console.log(adsInfo);
            } catch (error) {
                console.error('Error while loading active ads:', error);
                dispatch(showLoading(false));
            }
        }
    };

    useEffect(() => {
        loadActiveAds();
        // eslint-disable-next-line
    }, [contract]);

    const getCurrency = async (address) => {
        const isOnList = ContractsList?.[wallet?.chainId]?.[address];
        if (isOnList) {
            return isOnList;
        }
        const web3Instance = new Web3(window.ethereum);
        const tokenContract = new web3Instance.eth.Contract(ERC20ABI, address);
        const name = await tokenContract.methods?.name?.()?.call();
        const symbol = await tokenContract.methods?.symbol?.()?.call();
        const decimals = await tokenContract.methods?.decimals?.()?.call();
        const balanceOf = await tokenContract.methods?.balanceOf?.(wallet.accounts[0])?.call();

        return { name, symbol, decimals, balanceOf };
    };

    const handleModifyAd = () => {};

    const handleInputChange = (event, ad) => {
        // if (event.target.name === 'fiatValue') {
        //     handleInputFiat(event, ad);
        //     return;
        // }
        let value = event.target.value;
        let fiatValue = event.target.name === 'cryptoValue' ? parseFloat(value) * parseFloat(ad.fiatPricePerToken / fiatDecimals) : order.cryptoValue;
        let cryptoValue = event.target.name === 'fiatValue' ? parseFloat(value) / parseFloat(ad.fiatPricePerToken / fiatDecimals) : order.fiatValue;
        setOrder((state) => ({
            ...state,
            ...(event.target.name === 'cryptoValue' ? { fiatValue } : {}),
            ...(event.target.name === 'fiatValue' ? { cryptoValue } : {}),
            [event.target.name]: value
        }));
    };
    // eslint-disable-next-line
    const handleInputFiat = (event, ad) => {
        let fiatValue = event.target.value;
        if (fiatValue.length >= ad.fiatMinValue.length && fiatValue < ad.fiatMinValue) {
            fiatValue = ad.fiatMinValue;
        }
        const fiatMaxValue = ((ad.amount / 10 ** ad.currencyData.decimals) * (ad.fiatPricePerToken / fiatDecimals)).toString();
        console.log(fiatValue);
        console.log(fiatMaxValue);
        if (fiatValue > fiatMaxValue) {
            fiatValue = fiatMaxValue;
        }
        let cryptoValue = parseFloat(fiatValue) / parseFloat(ad.fiatPricePerToken / fiatDecimals);
        setOrder((state) => ({
            ...state,
            cryptoValue,
            fiatValue
        }));
    };

    const handleShowInputs = async (adId) => {
        setAdOrders([]);
        setOrder((state) => ({ ...state, cryptoValue: '', fiatValue: '', adId: order.adId === adId ? '' : adId }));
        if (order.adId !== adId) {
            const orders = await call('getOrdersByAd', [adId]);
            console.log(orders);
            setAdOrders(orders);
        }
    };

    return (
        <Styles className="tabs">
            <div className="tab-headers">
                <div className="tab-wrap">
                    {tabs.map((tab, index) => (
                        <Button
                            key={index}
                            className={`tab-header ${index === activeTab ? 'active' : ''}`}
                            onClick={() => handleTabClick(index)}
                            primary={index === activeTab ? 'primary' : 'secondary'}
                        >
                            {tab.label}
                        </Button>
                    ))}
                </div>
                <Filters info={info} />
            </div>
            <div className="tab-content">
                <div className="table-headers">
                    <div style={{ width: '20%' }}>
                        <span>Anunciante</span>
                    </div>
                    <div style={{ width: '15%' }}>
                        <span>Precio</span>
                    </div>
                    <div style={{ width: '20%' }}>
                        <span>Disponible / Lmite</span>
                    </div>
                    <div style={{ width: '15%' }}>
                        <span>Pago</span>
                    </div>
                    <div style={{ width: '15%' }}>
                        <span>Limite de Tiempo</span>
                    </div>
                    <div style={{ width: '15%' }}>
                        <span>Operacin</span>
                    </div>
                </div>
                {info.length > 0 ? (
                    info.map((e, index) => (
                        <>
                        { index ? <hr style={{color: '#dbdbdb'}} ></hr> : null }
                        <div
                            className="offer-wrap"
                            id={`adId_${e.adId}`}
                            key={e.adId}
                            style={order.adId === e.adId ? { backgroundColor: '#f1f3fb', borderColor: '#6379F455' } : {}}
                        >
                            <div className="offer-content">
                                <div className="offer-info">
                                    <div className="offer-user">
                                        <CardProfileUser username={e.user} wallet={e.owner} />
                                    </div>
                                    <span className="offer-fiat">
                                        <b style={{ fontSize: '20px' }}>${e.fiatPricePerToken / fiatDecimals}</b> {e.fiatCurrency}
                                    </span>
                                    <div className="offer-crypto">
                                        <p style={{ margin: '5px' }}>
                                            <span>{`${formatAmount(e.amount, parseInt(e.currencyData.decimals) || 18)}`}</span>
                                            <span style={{ marginLeft: '5px' }}>{e.currencyData.symbol || e.currency}</span>
                                        </p>
                                        <span>
                                            <span>
                                                ${e.fiatMinValue / fiatDecimals} - ${((e.amount / 10 ** e.currencyData.decimals) * (e.fiatPricePerToken / fiatDecimals)).toFixed(2)}
                                            </span>
                                            <span style={{ fontSize: '14px', marginLeft: '5px' }}>{e.fiatCurrency}</span>
                                        </span>
                                    </div>
                                    <span className="offer-pay">
                                        {e.paymentMethod.split(',').map((method) => (
                                            <p style={{ margin: 0 }} key={`${e.adId}_${method}`}>
                                                {method}
                                            </p>
                                        ))}
                                    </span>
                                    <span className="offer-expire">{e?.expirationTime ? e?.expirationTime / 60 : ''} min</span>
                                    <div className="offer-actions">
                                        {e?.owner?.toLowerCase() === wallet?.accounts?.[0]?.toLowerCase() ? (
                                            <Button primary={'secondary'} onClick={() => handleModifyAd(e.adId)}>
                                                Modificar
                                            </Button>
                                        ) : (
                                            <Button primary={order.adId === e.adId ? 'secondary' : 'primary'} onClick={() => handleShowInputs(e.adId)}>
                                                {order.adId === e.adId ? 'Cancelar' : tabs[activeTab].label}
                                            </Button>
                                        )}
                                    </div>
                                </div>
                            </div>
                            <div className="offer-mobile">
                                <div className="offer-mobile-user">
                                    <CardProfileUser username={e.user} wallet={e.owner} />

                                    <div className="pay">
                                        {e.paymentMethod.split(',').map((method) => (
                                            <p style={{ margin: 0 }} key={`${e.adId}_${method}`}>
                                                {method}
                                            </p>
                                        ))}
                                    </div>
                                </div>

                                <div className="offer-mobile-actions">
                                    <div className="offer-mobile-price">
                                        <span className="price">
                                             <b>$ {e.fiatPricePerToken / fiatDecimals}</b> <span className="currency">{e.fiatCurrency}</span>
                                        </span>
                                        <div className="limits">
                                            <div>
                                                <span className="amount">{`${formatAmount(e.amount, parseInt(e.currencyData.decimals) || 18)}`}</span>
                                                <span className="currency">{e.currencyData.symbol || e.currency}</span>
                                            </div>
                                            <div>
                                                <span>
                                                    
                                                    <span className="amount">
                                                        $ {e.fiatMinValue / fiatDecimals} - 
                                                        $ {((e.amount / 10 ** e.currencyData.decimals) * (e.fiatPricePerToken / fiatDecimals)).toFixed(2)}
                                                    </span>{' '}
                                                    <span className="currency">{e.fiatCurrency}</span>
                                                </span>
                                            </div>
                                        </div>
                                    </div>

                                    <div className="actions">
                                        <span className="expiration">
                                            <IoAlarmOutline /> {e?.expirationTime ? e?.expirationTime / 60 : ''} min
                                        </span>
                                        {e?.owner?.toLowerCase() === wallet?.accounts?.[0]?.toLowerCase() ? (
                                            <Button primary={'secondary'} onClick={() => handleModifyAd(e.adId)}>
                                                Modificar
                                            </Button>
                                        ) : (
                                            <Button primary={order.adId === e.adId ? 'secondary' : 'primary'} onClick={() => handleShowInputs(e.adId)}>
                                                {order.adId === e.adId ? 'Cancelar' : tabs[activeTab].label}
                                            </Button>
                                        )}
                                    </div>
                                </div>
                            </div>
                            {order.adId === e.adId ? (
                                <Container className="create-order">
                                    <hr style={{ color: '#e0e0e0', margin: '0 0 20px 0', height: '2px' }}></hr>
                                    <Row style={{ alignItems: 'flex-end' }}>
                                        <Col>
                                            <Form.Group>
                                                <Form.Label>Tu Usuario de Telegram</Form.Label>
                                                <Form.Control
                                                    key={`input_${e.adId}`}
                                                    type="text"
                                                    placeholder="t.me/..."
                                                    value={order.user}
                                                    onChange={(event) => handleInputChange(event, e)}
                                                    name="user"
                                                    style={order.adId === e.adId ? { borderColor: '#6379F455' } : {}}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col>
                                            <Form.Group>
                                                <Form.Label>
                                                    Envas <b>{e.isSeller ? e.fiatCurrency : e.currencyData.symbol || e.currency}</b>
                                                </Form.Label>
                                                <Form.Control
                                                    type="number"
                                                    placeholder={
                                                        !e.isSeller
                                                            ? '0.0'
                                                            : `${e.fiatMinValue / fiatDecimals} - ${(
                                                                  (e.amount / 10 ** e.currencyData.decimals) *
                                                                  (e.fiatPricePerToken / fiatDecimals)
                                                              ).toFixed(2)}`
                                                    }
                                                    onChange={(event) => handleInputChange(event, e)}
                                                    name={e.isSeller ? 'fiatValue' : 'cryptoValue'}
                                                    value={e.isSeller ? order.fiatValue : order.cryptoValue}
                                                    style={order.adId === e.adId ? { borderColor: '#6379F455' } : {}}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col>
                                            <Form.Group>
                                                <Form.Label>
                                                    Recibes <b>{e.isSeller ? e.currencyData.symbol || e.currency : e.fiatCurrency}</b>
                                                </Form.Label>
                                                <Form.Control
                                                    type="number"
                                                    placeholder={
                                                        e.isSeller
                                                            ? '0.0'
                                                            : `${e.fiatMinValue / fiatDecimals} - ${(
                                                                  (e.amount / 10 ** e.currencyData.decimals) *
                                                                  (e.fiatPricePerToken / fiatDecimals)
                                                              ).toFixed(2)}`
                                                    }
                                                    onChange={(event) => handleInputChange(event, e)}
                                                    name={e.isSeller ? 'cryptoValue' : 'fiatValue'}
                                                    value={e.isSeller ? order.cryptoValue : order.fiatValue}
                                                    style={order.adId === e.adId ? { borderColor: '#6379F455' } : {}}
                                                />
                                            </Form.Group>
                                        </Col>
                                        <Col>
                                            <Button primary={'primary'} onClick={() => handleCreateOrder(e)} style={{ marginTop: '60px' }}>
                                                {tabs[activeTab].label} {e.currencyData.symbol || e.currency}
                                            </Button>
                                        </Col>
                                    </Row>
                                    <div style={{ marginTop: '20px' }}>
                                        {adOrders?.length > 0 ? (
                                            <ul>
                                                {adOrders.slice(0, 3).map((j) => (
                                                    <li key={j.orderId}>
                                                        <p>
                                                            ID: <b>{j.orderId}</b>
                                                        </p>
                                                        <p>
                                                            Status: <b>{OrderStatusEnum[j.status]}</b>
                                                        </p>
                                                        <p>
                                                            User: <b>{j.user}</b>
                                                        </p>
                                                        <p>
                                                            ltima Actualizacin: <b>{new Date(Number(j.updatedAt) * 1000).toLocaleString()}</b>
                                                        </p>
                                                        <p>
                                                            Monto: <b>{`${formatAmount(j.value, parseInt(e.currencyData.decimals) || 18)}`}</b>
                                                        </p>
                                                    </li>
                                                ))}
                                            </ul>
                                        ) : (
                                            <span>Este anuncio no tiene rdenes an</span>
                                        )}
                                    </div>
                                </Container>
                            ) : null}
                        </div>
                        </>
                    ))
                ) : (
                    <></>
                )}
            </div>
        </Styles>
    );
};

Index.propTypes = {
    activeAds: PropTypes.array
};

export default Index;

const Styles = styled.div`
    .create-order {
        padding: 15px 25px;
        max-width: 100%;
    }
    .tab-headers {
        display: flex;
        align-items: center;
        justify-content: center;
        width: 100%;
        button {
            padding: 8px 10px;
            max-width: 120px;
            border-radius: 0;
            border: none;
        }
        button:first-child {
            border-radius: 8px 0 0 8px;
        }
        button:last-child {
            border-radius: 0 8px 8px 0;
        }
    }
    .tab-content {
        margin-top: 20px;
    }
    .table-headers {
        display: flex;
        align-items: center;
        justify-content: flex-start;
        padding: 15px 35px;
        text-align: left;
        @media screen and (max-width: 1300px) {
            display: none;
        }
    }
    .offer-wrap {
        border-radius: 8px;
        margin-bottom: 10px;
        display: flex;
        width: 100%;
        align-items: center;
        flex-direction: column;
        justify-content: space-between;
        @media screen and (max-width: 1200px) {
            border: none;
            padding: 15px 5px;
        }
        .offer-user {
            margin-bottom: 10px;
            justify-content: flex-start;
            @media screen and (max-width: 600px) {
                flex-direction: column;
            }
            width: 20%;
        }

        .offer-fiat {
            width: 15%;
        }

        .offer-crypto {
            width: 20%;
        }

        .offer-pay {
            width: 15%;
        }

        .offer-expire {
            width: 15%;
        }

        .offer-actions {
            width: 15%;
        }

        .offer-content {
            width: 100%;
            padding: 10px 20px;
            display: flex;
            justify-content: space-between;
            flex-direction: column;
            @media screen and (max-width: 1200px) {
                display: none;
            }
        }
        .offer-info {
            display: flex;
            align-items: flex-start;
            justify-content: space-between;
            margin-top: 1rem;
            @media screen and (max-width: 600px) {
                flex-wrap: wrap;
                font-size: 14px;
            }
        }
        .offer-user {
            display: flex;
            align-items: center;
            justify-content: flex-start;
            @media screen and (max-width: 600px) {
                flex-direction: column;
                font-size: 14px;
            }
        }
        @media screen and (max-width: 600px) {
            flex-direction: column;
        }
    }
    .tab-wrap {
        display: flex;
        align-self: flex-end;
        width: 100%;
    }

    .offer-mobile {
        @media screen and (min-width: 1200px) {
            display: none;
        }
        width: 100%;
        display: flex;
        flex-direction: column;
        margin-bottom: 1rem;

        .offer-mobile-user {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: flex-start;

            .pay {
                font-size: 12px;
                text-align: right;
            }
        }

        .offer-mobile-actions {
            width: 100%;
            display: flex;
            justify-content: space-between;
            align-items: flex-end;
            span.currency {
                    font-size: 12px;
                    color: #6a6a6a;
                }

            .actions button {
                padding: 7px 14px;
                font-size: 12px;
            }
            .expiration {
                font-size: 12px;
                color: #6a6a6a;
                text-align: right;
                display: block;
                margin-bottom: 15px;
            }
            .offer-mobile-price {
                display: flex;
                flex-direction: column;
                margin-top: 15px;

                
                .price {
                    font-size: 14px;
                    b {
                        font-size: 23px;
                    }
                    @media screen and (max-width: 1200px) {
                        margin-bottom: 5px;
                        b {
                            font-size: 18px;
                        }
                    }
                }
                .limits {
                    display: flex;
                    flex-direction: column;
                    div {
                        gap: 5px;
                        display: flex;
                        align-items: center;
                    }
                    .label {
                        font-size: 12px;
                        color: #6a6a6a;
                    }
                    .amount {
                        font-size: 12px;
                        color: #6a6a6a;
                    }
                }
            }
        }
    }

    @media screen and (max-width: 700px) {
        .tab-wrap {
            flex-direction: row;
            width: 100%;
            align-items: center;
            justify-content: space-between;
            margin-bottom: 10px;
            button {
                margin-right: 0;
                max-width: 100%;
            }
        }
        .tab-headers {
            flex-direction: column;
        }
    }
`;
