import React, { useEffect, useState } from 'react';
import { useHistory } from 'react-router-dom';
import {
  AVAJessicaMera,
  AVARobert,
  AVASherina,
  ICBackgroundLandingPage,
  ICDownload,
  ICLock,
  ICTelp,
  IMAirBnB,
  IMApp,
  IMCanon,
  IMDell,
  IMDropbox,
  IMHnM,
  IMMicrosoft
} from '../../assets';
import { LandingPageStyles } from './Styled';
import { LogoBrand } from '../../components/atoms';
import detectEthereumProvider from '@metamask/detect-provider'
import { FaTelegram } from 'react-icons/fa';

const Landingpage = () => {
  const history = useHistory();
  const initialState = { accounts: [], balance: "", chainId: "" }  /* Updated */
  const [wallet, setWallet] = useState(initialState)

  useEffect(() => {
    if (wallet.accounts.length > 0) {
      history.push(`/dashboard`);
    }
    // eslint-disable-next-line
  }, [wallet]);

  useEffect(() => {
    const refreshAccounts = (accounts) => {
      if (accounts.length > 0) {
        updateWallet(accounts)
      } else {
        // if length 0, user is disconnected
        setWallet(initialState)
      }
    }

    const refreshChain = (chainId) => {               /* New */
      setWallet((wallet) => ({ ...wallet, chainId }))      /* New */
    }                                                      /* New */

    const getProvider = async () => {
      const provider = await detectEthereumProvider({ silent: true })

      if (provider) {                                           
        const accounts = await window.ethereum.request(
          { method: 'eth_accounts' }
        )
        refreshAccounts(accounts)
        window.ethereum.on('accountsChanged', refreshAccounts)
        window.ethereum.on("chainChanged", refreshChain)  /* New */
      }
    }

    getProvider()

    return () => {
      window.ethereum?.removeListener('accountsChanged', refreshAccounts)
      window.ethereum?.removeListener("chainChanged", refreshChain)  /* New */
    }
    // eslint-disable-next-line
  }, [])

  const updateWallet = async (accounts) => {
    const balance = await window.ethereum.request({   /* New */
      method: "eth_getBalance",                                      /* New */
      params: [accounts[0], "latest"]})                               /* New */ 
    const chainId = await window.ethereum.request({                 /* New */ 
      method: "eth_chainId",                                         /* New */ 
    })                                                               /* New */ 
    setWallet({ accounts, balance, chainId })                        /* Updated */ 
  }

  const handleConnect = async () => {
    let accounts = await window.ethereum.request({
      method: "eth_requestAccounts",
    })
    updateWallet(accounts)
  }
    
  return (
    <LandingPageStyles>
      <img className="bg-layer" src={ICBackgroundLandingPage} alt="ng" />
      <div className="heading-section">
        <nav>
          <div className="custom-container">
            <LogoBrand />
            <div className="btn-wrapper">
              <button className="button filled" onClick={handleConnect}>
                Conectar
              </button>
            </div>
          </div>
        </nav>
        <header className="custom-container">
          <div className="right">
            <h1 className="title-section" style={{zIndex: 1}}>
            El Mejor Modo de Usar{" "}
              <span className="heading primary">Dinero.</span>
            </h1>
            
            <button className="button primary" onClick={handleConnect}>
              Descbrelo Aqu
            </button>
          </div>
          <aside>
            <div className="image-wrapper">
              <img src={IMApp} alt="app" />
            </div>
          </aside>
        </header>
      </div>

      <section className="about-application">
        <div className="custom-container">
          <h1 className="title-section center">
            <span className="heading primary">Conozca</span> la Aplicacin.
          </h1>
          <p className="paragraph">
          Tenemos excelentes funciones en la aplicacin 
          y es totalmente gratuito para todos los usuarios.
          </p>
          <div className="service-wrapper">
            <div className="card item-service">
              <div className="icon-wrapper">
                <img className="icon" src={ICTelp} alt="Contact" />
              </div>
              <h2>Soporte 24/7</h2>
              <p className="paragraph">
                Contamos con Soporte 24/7 para que pueda contactarnos cuando 
                quiera y le responderemos.
              </p>
            </div>
            <div className="card item-service">
              <div className="icon-wrapper">
                <img className="icon" src={ICLock} alt="lock" />
              </div>
              <h2>Privacidad de Datos</h2>
              <p className="paragraph">
              Nos aseguramos de que sus datos estn seguros en nuestra base de datos
               y encriptamos cualquier dato que nos enve.
              </p>
            </div>
            <div className="card item-service">
              <div className="icon-wrapper">
                <img className="icon" src={ICDownload} alt="Download" />
              </div>
              <h2>Descarga Facil</h2>
              <p className="paragraph">
                Wallet is 100% gratuita y ahora est disponible en 
                Google Play Store y App Store.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="partners-section">
        <div className="custom-container">
          <aside className="left">
            <h1 className="title-section">
              100+ <span className="heading primary">Partners </span>
              Confan.
            </h1>
            <p className="paragraph">
              Hemos alcanzado un nivel global y tenemos ms de 100 socios en todo el mundo.
            </p>
          </aside>
          <aside className="right">
            <div className="row">
              <div className="image-wrapper">
                <img src={IMAirBnB} alt="Icon AirBnB" />
              </div>
              <div className="image-wrapper">
                <img src={IMCanon} alt="Icon Canon" />
              </div>
              <div className="image-wrapper">
                <img src={IMDell} alt="Partner Dell" />
              </div>
            </div>
            <div className="row">
              <div className="image-wrapper">
                <img src={IMMicrosoft} alt="Microsoft" />
              </div>
              <div className="image-wrapper">
                <img src={IMDropbox} alt="Dropbox" />
              </div>
              <div className="image-wrapper">
                <img src={IMHnM} alt="HnM" />
              </div>
            </div>
          </aside>
        </div>
      </section>
      <section className="features-section">
        <div className="custom-container">
          <div className="left">
            <div className="image-wrapper">
              <img src={IMApp} alt="wallet app" className="img-wallet"/>
            </div>
          </div>
          <div className="right">
            <h1 className="title-section center">
              Todas las <span className="heading primary">Funciones </span>
              de la Aplicacion
            </h1>
            <div className="card feature-item">
              <div className="title">
                <h3 className="number">1.</h3>
                <h3>Bajas Comisiones</h3>
              </div>
              <p className="paragraph">
                Solo tenemos un recargo del 1% de todas las transacciones exitosas de Wallet.
              </p>
            </div>
            <div className="card feature-item">
              <div className="title">
                <h3 className="number">2.</h3>
                <h3>Seguridad de Datos</h3>
              </div>
              <p className="paragraph">
                Todos sus datos estan protegidos en nuestro sistema y encriptados
              </p>
            </div>
            <div className="card feature-item">
              <div className="title">
                <h3 className="number">3.</h3>
                <h3>Facil de Usar</h3>
              </div>
              <p className="paragraph">
                Wallet presenta un diseo moderno y elegante y no es complicado.
              </p>
            </div>
          </div>
        </div>
      </section>
      <section className="users-section">
        <div className="custom-container">
          <h1 className="title-section center">
            Que Dicen Nuestros <span className="heading primary">Usuarios.</span>
          </h1>
          <p className="paragraph">
          Tenemos excelentes funciones en la aplicacin y es totalmente gratuita para todos los usuarios.
          </p>
          <div className="card-users-wrapper">
            <div className="card user">
              <div className="image-profile-wrapper">
                <img src={AVASherina} alt="user 1" />
              </div>
              <h2>Sherina Chaw</h2>
              <p className="paragrah">
                Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.
              </p>
            </div>
            <div className="card user">
              <div className="image-profile-wrapper">
                <img src={AVAJessicaMera} alt="user 2" />
              </div>
              <h2>Jessica Mera</h2>
              <p className="paragrah">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.
              </p>
            </div>
            <div className="card user">
              <div className="image-profile-wrapper">
                <img src={AVARobert} alt="user 1" />
              </div>
              <h2>Robert Chandler</h2>
              <p className="paragrah">
              Lorem ipsum dolor sit amet, consectetur adipiscing elit, sed do eiusmod tempor.
              </p>
            </div>
          </div>
        </div>
      </section>
      <footer>
        <div className="custom-container">
          {/* <h1 className="logo">CambioVirtual.com</h1>
          <p className="text">
            Simplifique sus necesidades financieras y ahorre tiempo todo en un App
          </p>
          <div className="divider"></div> */}
          <div className="contact">
            <div>
              <p className="text"> {new Date().getFullYear()} CambioVirtual.com</p>
            </div>
            <div className="contact-email">
            <a target='_blank' href='https://t.me/CambioVirtual' className='telegram-wrap' rel="noreferrer">
              <FaTelegram className="telegram-icon" />
              <p className="text-regular telegram">CambioVirtual</p>
            </a>
            </div>
          </div>
        </div>
      </footer>
    </LandingPageStyles>
  );
};

export default Landingpage;
