import React, { useState }  from 'react';
import styled from 'styled-components';
import { Cardwrapper, HeadingContent } from '../../components';
import { createWallet, decipherData } from "../../utils";
import Button from "../../components/atoms/Button";
import { requestWallet } from '../../config/Redux/actions/walletActions';


const WalletPage = () => {
  const [password, setPassword] = useState('');
  const [wallets, setWallets] = useState({
    name: '',
    ownerAddress: '', 
    ownerKey: '', 
    ownerRecoveryAddress: '', 
    ownerRecoveryKey: ''
  });

  const [encodeString, setEncodeString] = useState({
    string: '',
    key: '',
    result: ''
  });

  const onWalletCreate = async () => {
    const { address: ownerAddress, privateKey: ownerKey } = createWallet(password)
    const { address: ownerRecoveryAddress, privateKey: ownerRecoveryKey } = createWallet('testRecovery')
  
    const newWallet = {
      ownerAddress, ownerKey, ownerRecoveryAddress, ownerRecoveryKey
    }
    setWallets({...wallets, ...newWallet})
    requestWallet({name: wallets.name, ...newWallet});
  }

  const onDecode = () => {
    let result = decipherData(encodeString.key, encodeString.string);
    if (!result) {
      result = 'Wrong String/Key pair.'
    }
    setEncodeString( state => ({
      ...state,
      result
    }));
  }

  const handleInputChange = (event) => {
    setPassword(event.target.value);
  };

  const handleStringChange = (event) => {
    setEncodeString( state => ({
      ...state,
      string: event.target.value
    }));
  };

  const handleKeyChange = (event) => {
    setEncodeString( state => ({
      ...state,
      key: event.target.value
    }));
  };

  const handleName = (event) => {
    setWallets({...wallets, name: event.target.value});
  };

 
  
  return (
    <Cardwrapper>
      <StyledTopupPage>
        <HeadingContent>Tus Monederos</HeadingContent>
        <div className="body-content">
          <div>
                {exampleWallets.map((wallet) => (
                  <div key={wallet.id}>
                    <p><b>{wallet.name}</b> </p>
                    <p>
                      {wallet.supportBlockchains.join(", ")}
                    </p>
                    <p>Direccin: <a rel="noreferrer" target="_blank" href={`https://mumbai.polygonscan.com/address/${wallet.publicKey}`}>{wallet.publicKey}</a></p>
                  </div>
                ))}
              </div>
              <div>
                
                {
                  Object.keys(wallets).map((wallet) => (
                    <p key={wallet}>{wallet} { `${wallets[wallet].slice(0, 7)}...${wallets[wallet].slice(-7)}`}</p>    
                  ))
                }
              </div>
              <input type="text" placeholder='Alias' value={wallets.name} onChange={handleName}/>
              <input type="text" placeholder='Contrasea' value={password} onChange={handleInputChange}/>
              <Button
                primary={"primary"}
                onClick={onWalletCreate}> 
                Crear Monedero 
              </Button>

              <input type="text" placeholder='String' value={encodeString.string} onChange={handleStringChange}/>
              <input type="text" placeholder='Key' value={encodeString.key} onChange={handleKeyChange}/>
              <input type="text" placeholder='Result' value={encodeString.result}/>
              <Button
                primary={"primary"}
                onClick={onDecode}> 
                Decode
              </Button>
        </div>
      </StyledTopupPage>
    </Cardwrapper>
  );
};

export default WalletPage;


const exampleWallets = [
  {
    id: "1",
    alias: "Principal",
    supportBlockchains: ["MATIC", "ETH", "BSC"],
    numero: "000001",
    publicKey: '',
  },
  {
    id: "2",
    alias: "Caja de Ahorros",
    supportBlockchains: ["MATIC", "ETH", "BSC"],
    numero: "000002",
    publicKey: '',
  },
];

const StyledTopupPage = styled.div`
  padding: 30px;
  .body-content {
    display: flex;
    flex-direction: column;
    gap: 1rem;
    .item {
      padding: 30px 20px;
      display: flex;
      gap: 1rem;
      p {
        margin-bottom: 0;
        overflow: auto
      }
      .number {
        font-family: Nunito Sans;
        font-style: normal;
        font-weight: bold;
        font-size: 18px;
        line-height: 25px;
        color: #6379f4;
      }
      .publicKey {
        font-size: 15px;
        word-break: break-all;
        margin-bottom: 1rem;
      }
    }
  }
`;
