import PropTypes from 'prop-types';
import React from 'react';
import styled from 'styled-components';
import { Cardwrapper } from '../../atoms';
import { IoClose } from 'react-icons/io5';

const Modal = ({ children, showModal, closeModal }) => {
  // const [isShow, setIsShow] = useState(false);

  if (!showModal) return null;

  return (
    <StyledModal>
      <Cardwrapper className="content-wrapper">
        <div className="header">
          <h3 className="text-heading">Autorizacin Requerida</h3>
          <IoClose onClick={closeModal} style={{fontSize: '1.75rem', color: '#3A3D42'}}/>
        </div>
        <p className="modal-description">
          Debe ingresar su contrasea para continuar.
        </p>
        <div className="body">{children}</div>
      </Cardwrapper>
      <div className="bg-layer" onClick={closeModal}></div>
    </StyledModal>
  );
};

Modal.propTypes = {
  children: PropTypes.element.isRequired,
};

export default Modal;

const StyledModal = styled.div`
  width: 100vw;
  height: 100vh;
  position: fixed;
  z-index: 1;
  top: 150px;
  left: 0;
  display: flex;
  align-items: flex-start;
  justify-content: center;
  .content-wrapper {
    z-index: 1;
    padding: 30px;
    width: 90%;
    max-width: 500px;
    .header {
      display: flex;
      justify-content: space-between;
      margin-bottom: 20px;
    }
    .modal-description {
      margin-bottom: 32.36px;
    }
  }
  .bg-layer {
    background: #3a3d42;
    opacity: 0.5;
    width: 100vw;
    z-index: -1;
    height: 100vh;
    left: 0;
    top: 0;
    position: fixed;
  }
`;
